import { Component, OnInit, Input, Output, EventEmitter,  } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { toArray, isObject, fromPairs, keys, includes } from 'lodash';
import { DataService } from 'src/app/services/data.service';
import { formatDate } from '@angular/common';
import { UsersdialogComponent } from '../usersdialog/usersdialog.component';

@Component({
  selector: 'app-html-table',
  templateUrl: './html-table.component.html',
  styleUrls: ['./html-table.component.scss']
})
export class HtmlTableComponent implements OnInit {
  total: any;
// userDataSource=Constants.USERS;
    //for table header config
    constructor(private modalDialog:MatDialog,private apiService:DataService)
    {
      
    }
      //for manage Input table
  // userDataSource=Constants.USERS;
  //for table header config
  p: number = 1;
  specialFields: Array<String> = ['user', 'created_by', 'uploaded_to_crm', 'completed', 'isLocked',  'isInterrupted', 'saved', 'isStopped', 'Selected Option', 'disabled', 'required', 'video', 'is_active']
  selectAllFields: Array<String> = ['deleteItems', 'upload To CRM'];
  sortTableHeaders: any = [];
  @Input() tableData: any = []
  @Input() tableColumnHeaders: string[] = [];
  @Input() tableSortingColumnHeaders: any[] = [];
  @Input() tableColumns: string[] = [];
  @Input() menu: string;
  @Input() pageName: string = "";
  @Output() onCellClicked: any = new EventEmitter<any>();
  @Output() onCheckboxClicked: any = new EventEmitter<any>();
  @Output() onUploadCheckboxClicked: any = new EventEmitter<any>();
  @Output() onHeaderCheckboxClicked: any = new EventEmitter<any>();
  @Output() onHeaderUploadCheckboxClicked: any = new EventEmitter<any>();
  @Output() onRowClicked: any = new EventEmitter<any>();
  @Output() onToggle: any = new EventEmitter<any>();
  @Output() onPaginationChange: any = new EventEmitter<any>();
  @Output() sortClicked: any = new EventEmitter<any>();
  @Input() isSortTable: boolean = false;
  @Input() numberOfRecords: number = 0;
  @Input() recordsPerPage: number = 10;
  @Input() currentPage: number = 1;
  @Input() shouldBulkDelete: boolean = false;
  @Input() shouldBulkUpload: boolean = false;
  @Input() isPaginationRequired: boolean = true;
  redButtonsTextList = ['no', 'incorrect', 'false']
  //for manage Input table
  onCellClick(event, index, url, col) {
    const data = [event, [index + (this.p - 1) * this.recordsPerPage], url, col];
    this.onCellClicked.emit(data);
  }

  onRowClick(event, index, data) {
    const item = [event, [index + (this.p - 1) * this.recordsPerPage], data];
    this.onRowClicked.emit(item);
  }


  onIconClick(icon, index, data) {
    const value = [icon, [index + (this.p - 1) * this.recordsPerPage], data];
    this.onCellClicked.emit(value);
  }

  onCheckboxChange(status, index, data) {
    this.tableData[index]['checkbox'] = status;
    const value = [[index + (this.p - 1) * this.recordsPerPage], data];
    this.onCheckboxClicked.emit(value);
  }

  onUploadCheckboxChange(status, index, data) {
    this.tableData[index]['upload_to_crm'] = status;
    const value = [[index + (this.p - 1) * this.recordsPerPage], data];
    this.onUploadCheckboxClicked.emit(value);
  }

  onHeaderCheckboxChange(status) {
    this.tableData.map(data => {
      data['checkbox'] = status;
    })
    this.onHeaderCheckboxClicked.emit(status);
  }

  onHeaderUploadCheckboxChange(status) {
    this.tableData.map(data => {
      data['upload_to_crm'] = status;
    })
    this.onHeaderUploadCheckboxClicked.emit(status);
  }

  toggleStatus(value, index, item, itemValue, itemData) {
    // Call API to update data
    this.tableData[index][item] = value;
    const data = [value, [index + (this.p - 1) * this.recordsPerPage], item, itemValue, itemData];
    this.onToggle.emit(data);
  }

  getArrayFromObject(data) {
    var result = Object.keys(data).map(function (key) {
      return [key, data[key]];
    });
    return result;
  }

  toLowercase(text) {
    return String(text).toLowerCase();
  }

  getKeys(data) {
    let val = keys(data);
    return val;
  }

  isObject(data) {
    return isObject(data);
  }

  formatDateValue(date) {
    let convertedDate = new Date(date.toString());
    return formatDate(convertedDate, 'MM/dd/yyyy', 'en-US');
  }

  formatTimeValue(time) {
    let convertedDate = new Date(time.toString());
    return formatDate(convertedDate, 'hh:mm a', 'en-US');
  }


  getButtonColor(item) {
    return this.redButtonsTextList.includes(String(item).toLowerCase()) ? 'false-text' : 'true-text';
  }

  isExist(key) {
    let isExist = includes(this.tableColumnHeaders, key);
    return isExist;
  }

  getTextRenderCondition(key) {
    return !includes(this.specialFields, key);
  }

  isSelectAllFields(key) {
    let keyExist = !includes(this.selectAllFields, key);
    return keyExist;
  }

  ngOnInit(): void {
    // Set checkbox as false for survey history columns

    if(this.isSortTable) {
      if(this.tableSortingColumnHeaders.length <= 0) {
        let sortHeaders: any = [];
        this.tableColumnHeaders.forEach((value, index)=>{
          let obj = {
            "columnName": this.tableColumns[index],
            "headerName": value,
            "isActive": false,
            "sortType": "none"
          };
          sortHeaders.push(obj);
      })
      this.tableSortingColumnHeaders = sortHeaders;
      }
    }    
  }

  onPageChange(event) {
    this.onPaginationChange.emit(event);
  }

  resetUploadColumnData() {
    this.tableData.map(data => {
      data['upload_to_crm'] = false;
    })
  }

  resetDeleteColumn() {
    this.tableData.map(data => {
      data['checkbox'] = false;
    })
  }

  headerSort(item, heading, index) {
    this.tableSortingColumnHeaders.map((data, arrIndex)=>{
      if(index == arrIndex) {
        data.isActive = true;
        data.sortType = data.sortType == 'none' ? 'ASC' : data.sortType == 'ASC' ? 'DESC' : 'none'
      }
      else {
        data.isActive = false;
        data.sortType = 'none';
      }
      return data;
    });
    let obj = {
      index: index,
      updatedObj : this.tableSortingColumnHeaders
    }
    this.sortClicked.emit(obj);
  }

}
