import { DataService } from './../../services/data.service';
import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {

  password = "";
  confirmPassword = "";
  token = "";
  buttonDisabled = false;
  showPassword = false;
  showNewPassword = false;
  isSuperAdmin = this.router.url.includes('super-admin');
  constructor(private _snackBar: MatSnackBar, private activatedRoute: ActivatedRoute, private dataService: DataService, private router: Router) { }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((param) => {
      this.token = param.token;
    })
  }
  onPasswordChange(password: string) {
    this.password = password;
  }
  onConfirmPasswordChange(password: string) {
    this.confirmPassword = password;
  }
  togglePassword(event: any) {
    this.showPassword = !this.showPassword;
  }

  toggleNewPassword(event: any) {
    this.showNewPassword = !this.showNewPassword;
  }

  async onSaveClick() {
    if (this.password === this.confirmPassword) {
      // Code for reset password
      this.buttonDisabled = true;
      const res = !this.isSuperAdmin ? await this.dataService.changePassword(this.password, this.token) : await this.dataService.superAdminChangePassword(this.password, this.token);
      if (res.statusCode && res.statusCode === 200) {
        this._snackBar.open(res.message, '', {
          duration: 2000,
          panelClass: ['greenSnackBar'],
        });
        this.buttonDisabled = false;
        this.backToLogin();
      } else {
        this._snackBar.open(res.message, '', {
          duration: 2000,
          panelClass: ['redSnackBar'],
        });
        this.buttonDisabled = false;
      }
    } else {
      this._snackBar.open('Pasword doesn\'t match', '', {
        duration: 2000,
        panelClass: ['redSnackBar'],
      });
    }
  }

  backToLogin() {
    var isSuperAmdin = this.router.url.includes('super-admin');
    if(isSuperAmdin)
    {
     this.router.navigate(['/super-admin'])
    }else 
    {
    this.router.navigate(['/']);
    }
  }

}
