import { Component, Inject, OnInit, Input } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { constants } from 'buffer';
import { Forms } from 'src/app/models/forms.model';
import { Tenants } from 'src/app/models/tenats.model';
import { User } from 'src/app/models/user.model';
import { DataService } from 'src/app/services/data.service';
import { ProfileConfirmModalComponent } from 'src/app/common/profile-confirm-modal/profile-confirm-modal.component';

import { Constants } from '../../constants';
import { toArray, isObject, fromPairs, keys, includes } from 'lodash';
import { MatSnackBar } from '@angular/material/snack-bar';
import { NgxPermissionsService } from 'ngx-permissions';

@Component({
  selector: 'usersdialog',
  templateUrl: './usersdialog.component.html',
  styleUrls: ['./usersdialog.component.scss']
})
export class UsersdialogComponent implements OnInit {
  roles: any = [];

  loader: boolean = false;
  form: FormGroup;
  selectedRole: any = '';
  mode: string = this.data.mode;
  newUser = new FormData();
  editUser = new FormData();
  currentFormDetail: any = [];
  defaultProfile: any = null;
  userId: String = ""
  @Input() page: string = 'admin';
  isLoading: boolean = false;

  password = "";
  confirmPassword = "";
  resetPassword = "";
  confirmResetPassword = "";
  passwordValue = ""
  token = "";
  passwordErrorMsg= "";
  buttonDisabled = false;
  showPassword = false;
  showNewPassword = false;
  showResetPassword = false;
  showConfirmResetPassword = false;
  confirmPasswordInputType = "password";
  newPasswordInputType = "password";
  resetPasswordInputType = "password";
  resetConfirmPasswordInputType = "password";
  isRandomPassword:boolean = false;
  resetCheckbox: boolean =  false;

  modalName = this.data.modalname;
  status: boolean = false;

  permissions: string | null = "";
  permissionsArray: Array<String> = [];

  constructor( private permissionsService: NgxPermissionsService, private _snackBar: MatSnackBar, private apiService: DataService, private modalDialog: MatDialog, public dialogRef: MatDialogRef<UsersdialogComponent>, private fb: FormBuilder, @Inject(MAT_DIALOG_DATA) public data: any) {
    if (this.mode == 'edit') {
      if (this.data && this.data.editables && this.data.editables.user && this.data.editables.user.profile) {
        this.defaultProfile = this.data.editables.user.profile != null ? this.data.editables.user.profile : null;
        this.status = this.data.editables.is_active != null ? this.data.editables.is_active : null;
        this.userId =  this.data.editables && this.data.editables.id || "";
      }
      else {
        this.defaultProfile = this.data.editables.profile != null ? this.data.editables.profile : null;
        this.userId =  this.data.editables && this.data.editables.userid || "";
      }
    }
    if (this.data && this.data.page && this.data.page === 'super-users') {
      this.userId =  this.data.editables && this.data.editables.userid || "";
      this.page = this.data.page;
    }
  }
  changeProfile(event) {
    var modalName = this.modalName.split('modal')[0];

    if (this.mode == 'add' || this.mode == 'edit') {
      if (modalName === "tenants") {
        this.newUser.append('logo', event.target.files[0], event.target.files[0].name);
        this.editUser.append('logo', event.target.files[0], event.target.files[0].name);
      }
      else {
        this.newUser.append('picture', event.target.files[0], event.target.files[0].name);
        this.editUser.append('picture', event.target.files[0], event.target.files[0].name);
      }
    }
    var fileReader = new FileReader()
    if (event.target.files.length > 0) {
      fileReader.onload = (e) => {
        this.defaultProfile = e.target?.result;
      }
      fileReader.readAsDataURL(event.target.files[0]);
    }
  }

  onTextValueChange(event: any, type: any) {
    if(event.target.id == 'confirm-password') {
      this.confirmPassword = event.target.value;
    }
    else if(event.target.id == 'new-password') {
      this.password = event.target.value;
    }
    else if(event.target.id == 'confirm-reset-password') {
      this.confirmResetPassword = event.target.value;
    }
    else {
      this.resetPassword = event.target.value;
    }
  }

  validatePassword() {
    if( 
    ((this.confirmResetPassword == "" && this.resetPassword == "" )  ||  (this.confirmResetPassword.length >= 8 && this.resetPassword.length >= 8 &&  this.confirmResetPassword == this.resetPassword ))
    &&
    ((this.confirmPassword == "" && this.password == "" )  ||  (this.confirmPassword.length >= 8 && this.password.length >= 8 &&  this.confirmPassword == this.password ) )
    )  
    {
      return true;
    }

    return false;
  }

  setRadomPassword(randomPassword) { 
    this.isRandomPassword = randomPassword;
    if(this.isRandomPassword) {
      this.form.removeControl('newPassword');
      this.form.removeControl('confirmPassword');
      this.password = "";
      this.confirmPassword = "";
    }
    else {
      this.form.addControl('newPassword', new FormControl('', [Validators.required, Validators.minLength(8)]));
      this.form.addControl('confirmPassword', new FormControl('', [Validators.required, Validators.minLength(8)]));
    }
  }

  onEnterClick(event: any) {
    // handle Enter click functionality
  }

  appendIconClick(event: any) {
    if(event.target.id == 'confirm-password') {
      this.showPassword = !this.showPassword;
      this.confirmPasswordInputType = this.showPassword ? 'text' : 'password';
    }
    else if(event.target.id == 'new-password') {
      this.showNewPassword = !this.showNewPassword;
      this.newPasswordInputType = this.showNewPassword ? 'text' : 'password';
    }
    else if(event.target.id == 'confirm-reset-password') {
      this.showConfirmResetPassword = this.showConfirmResetPassword;
      this.resetConfirmPasswordInputType = this.showConfirmResetPassword ? 'text' : 'password';
    }
    else {
      this.showResetPassword = !this.showResetPassword;
      this.resetPasswordInputType = this.showResetPassword ? 'text' : 'password';
    }
    event.stopPropagation();
  }

  editProfileImage(event: any) {
    event.preventDefault();
    let dialogBoxSettings = {
      width: '500px',
      margin: '0 auto',
      "max-height": 'fit-content',
    }
    this.modalDialog.open(ProfileConfirmModalComponent, dialogBoxSettings).afterClosed().subscribe(async (result) => {
      if(result && result.event == 'close') {
        // Close functionality
      }
      else if(result && result.event == 'remove') {  
        this.isLoading = true;  
        const currentUserId = localStorage.getItem('userId');
        const loggedAs = localStorage.getItem('logginedAs')
        if(this.data && this.data.page && this.data.page == 'tenants') {
          if(currentUserId == this.userId) {
            let res = await this.apiService.deleteProfileImageOfOwnTenant();
            this.isLoading = false; 
            if (res.message) {
              this.defaultProfile = "";
              this._snackBar.open(res.message, '', {
                duration: 2000,
              })
            }
          }
          else {
            let res = await this.apiService.deleteProfileImageOfTenant(this.userId);
            this.isLoading = false; 
            if (res.message) {
              this.defaultProfile = "";
              this._snackBar.open(res.message, '', {
                duration: 2000,
              })
            }
          }
        }
        // Admin remove functionality
        else if(loggedAs == 'admin') {
          if(currentUserId == this.userId) {
            let res = await this.apiService.deleteOwnProfileImage();
            this.isLoading = false; 
            if (res.message) {
              this.defaultProfile = "";
              this._snackBar.open(res.message, '', {
                duration: 2000,
              })
            }
          }
          else {
            let res = await this.apiService.deleteProfileImageOfUser(this.userId);
            this.isLoading = false; 
            if (res.message) {
              this.defaultProfile = "";
              this._snackBar.open(res.message, '', {
                duration: 2000,
              })
            }
          }
        }
        // Super admin remove functionality
        else {
          if(currentUserId == this.userId) {
            let res = await this.apiService.deleteSuperAdminOwnProfileImage();
            this.isLoading = false; 
            if (res.message) {
              this.defaultProfile = "";
              this._snackBar.open(res.message, '', {
                duration: 2000,
              })
            }
          }
          else {
            let res = await this.apiService.deleteSuperAdminProfileImageOfUser(this.userId);
            this.isLoading = false; 
            if (res.message) {
              this.defaultProfile = "";
              this._snackBar.open(res.message, '', {
                duration: 2000,
              })
            }
          }
        }  
      }
      else if(result && result.event == 'change') {
        var form = document.getElementById('user-form'),
        input = form?.getElementsByClassName('profile-image-change-class');
        let element:HTMLElement = input![0] as HTMLElement;
        element.click();
      }
      else {
        // CLOSE
      }

    });
  }

  //get all form controls
  get f() {
    return this.form.controls;
  }
  //save data from form
  async saveUser() {
    this.loader = true;
    var modalName = this.modalName.split('modal')[0];

    if (modalName == "users") {
      if (this.defaultProfile == null) {
        this.editUser.append("picture", this.defaultProfile);
        this.newUser.append("picture", this.defaultProfile);
      }
      if (this.mode == 'edit') {
        let values = this.getDirtyValues(this.form);
        Object.keys(values).map(e => {
          this.editUser.append(e, this.form.getRawValue()[e]);
        });

        let userid = this.data.editables['userid'];
        if (this.page == 'super-users') {
          let resetPassword = values ? values['resetPassword'] : '';
          let passwordObj = { 
            "password" : resetPassword,
          };
          if(resetPassword) {
              let response = await this.apiService.resetSuperAdminPassword(passwordObj, userid);
              if (response['statusCode'] == 200) {
                await this.apiService.updateSuperUser(this.editUser, userid).subscribe(async res => {
                  if (res['statusCode'] == 200) {
                    this.loader = await false;
                    await this.closeModal();
                  }
                })
              }
          }

          this.apiService.updateSuperUser(this.editUser, userid).subscribe(async res => {
            if (res['statusCode'] == 200) {
              this.loader = await false;
              await this.closeModal();
            }
          })
        }
        else {
          let resetPassword = values ? values['resetPassword'] : '';
          let passwordObj = { 
            "password" : resetPassword,
          };
          if(resetPassword) {
              let response = await this.apiService.resetPassword(passwordObj, userid);
              if (response['statusCode'] == 200) {
                await this.apiService.updateUser(this.editUser, userid).subscribe(async res => {
                  if (res['statusCode'] == 200) {
                    this.loader = await false;
                    await this.closeModal();
                  }
                })
              }
          }
          this.apiService.updateUser(this.editUser, userid).subscribe(async res => {
            if (res['statusCode'] == 200) {
              this.loader = await false;
              await this.closeModal();
            }
          })
        }

      } else {
        // this.newUser.append("role_id",this.form.getRawValue()['role'])
        // this.newUser.append("first_name",this.form.getRawValue()['firstname'])
        // this.newUser.append("email",this.form.getRawValue()['email'])
        // this.newUser.append("last_name",this.form.getRawValue()['lastname'] !='' ? this.form.getRawValue()['lastname'] : null)
        // this.newUser.append("display_name",this.form.getRawValue()['displayname'] !='' ? this.form.getRawValue()['displayname'] : null)
        if(!this.isRandomPassword) {
          if(this.confirmPassword != this.password) {
            this._snackBar.open('Passwords Doesn\'t Match', '', {
              duration: 2000,
              panelClass: ['redSnackBar']
            })
            this.loader = await false;
            return ;
          }
        }
        else {
          this.form.removeControl('newPassword');
          this.form.removeControl('confirmPassword');
        }

        let values = this.getDirtyValues(this.form);
        Object.keys(values).map(e => {
          this.newUser.append(e, this.form.getRawValue()[e] != '' ? this.form.getRawValue()[e] : null);
        });

        if(this.isRandomPassword) {
          this.newUser.append("password", "");
        }
        else {
          this.newUser.append("password", this.confirmPassword);
        }


        if (this.page == 'super-users') {
          this.apiService.createSuperUser(this.newUser).subscribe(async res => {
            if (res.message == 'User Created Successfully') {
              this.loader = await false;
              await this.closeModal();
            }
          });
        }
        else {
          this.apiService.createUser(this.newUser).subscribe(async res => {
            if (res.message == 'User Created Successfully') {
              this.loader = await false;
              await this.closeModal();
            }
          });
        }
      }
    }
    else if (modalName === "tenants") {
      if (this.defaultProfile == null) {
        this.editUser.append("logo", this.defaultProfile);
        this.newUser.append("logo", this.defaultProfile);
      }
      if (this.mode == 'edit') {
        let values = this.getDirtyValues(this.form);
        Object.keys(values).map(e => {
          this.editUser.append(e, this.form.getRawValue()[e]);
        });
        let id = this.data.editables['id'];
        let updatedDetails = await this.apiService.updateTenents(this.editUser, id)
        if (updatedDetails['statusCode'] == 200) {
          this.loader = await false;
          this.dialogRef.close({ event: "edit" });
        }
      }
      else {

        if(!this.isRandomPassword) {
          if(this.confirmPassword != this.password) {
            this._snackBar.open('Passwords Doesn\'t Match', '', {
              duration: 2000,
              panelClass: ['redSnackBar']
            })
            this.loader = await false;
            return ;
          }
        }
        else {
          this.form.removeControl('newPassword');
          this.form.removeControl('confirmPassword');
        }
        this.newUser.append('is_active', this.status ? new Boolean(this.status).toString() : new Boolean(false).toString());
        let values = this.getDirtyValues(this.form);
        Object.keys(values).map(e => {
          this.newUser.append(e, this.form.getRawValue()[e]);
        });
        let obj = {}
        for (var pair of this.newUser.entries()) {
          if (pair[0] == 'is_active') {
            obj[pair[0]] = (pair[1] == 'true');
          }
          else {
            obj[pair[0]] = pair[1];
          }
        }

        if(this.isRandomPassword) {
          this.newUser.append("password", "");
        }
        else {
          this.newUser.append("password", this.confirmPassword);
        }


        let updatedDetails = await this.apiService.postTenent(this.newUser);
        if (updatedDetails['statusCode'] == 200 || updatedDetails['statusCode'] == 201) {
          this.loader = await false;
          this.dialogRef.close({ event: "add" });
        }

      }
    }
  }
  //for change status in addtenants modal
  changeStatus(status) {
    let { checked } = status;
    this.status = checked;
    var modalName = this.modalName.split('modal')[0];

    if (this.mode == 'add' || this.mode == 'edit') {
      if (modalName === "tenants") {
        this.newUser.append('is_active', new Boolean(this.status).toString());
        this.editUser.append('is_active', new Boolean(this.status).toString());
      }
      if (modalName === 'users' && this.mode == 'edit') {
        this.newUser.append('is_active', new Boolean(this.status).toString());
        this.editUser.append('is_active', new Boolean(this.status).toString());
      }
    }

  }


  onPasswordChange(password: string) {
    this.password = password;
  }
  onConfirmPasswordChange(password: string) {
    this.confirmPassword = password;
  }
  togglePassword(event: any) {
    this.showPassword = !this.showPassword;
  }

  toggleNewPassword(event: any) {
    this.showNewPassword = !this.showNewPassword;
  }

  onCancelClick() {
    this.dialogRef.close({ event: "close" });
  }

  //get values of controls which has changed
  getDirtyValues(form: any) {
    let dirtyValues = {};

    Object.keys(form.controls)
      .forEach(key => {
        let currentControl = form.controls[key];

        if (currentControl.dirty) {
          if (currentControl.controls)
            dirtyValues[key] = this.getDirtyValues(currentControl);
          else
            dirtyValues[key] = currentControl.value;
        }
      });

    return dirtyValues;
  }

  //for get all roles
  async getRoles() {
    this.apiService.getAllRoles().subscribe(async res => {
      this.roles = await Array.from(res['roles']);
      if (this.data.mode == 'edit') {
        res['roles'].filter(e => {
          if (e.name.toLowerCase() == this.data.editables['role_id'].toLowerCase()) {
            this.selectedRole = e.id;
          }
        });
      }
    });
  }

  //for get all roles
  async getSuperAdminRoles() {
    this.apiService.getAllSuperAdminRoles().subscribe(async res => {
      this.roles = await Array.from(res['roles']);
      if (this.data.mode == 'edit') {
        res['roles'].filter(e => {
          if (e.name.toLowerCase() == this.data.editables['role_id'].toLowerCase()) {
            this.selectedRole = e.id;
          }
        });
      }
    });
  }

  //for create a dynamic form
  createForm() {
    this.form = this.fb.group({});
    this.modalName = this.modalName.split('modal')[0];
    var form: any = Constants.Forms.filter(e => e.formname == this.modalName ? e.formcontrols : '');
    var formcontrols = form[0].formcontrols;
    //create formcontrols by mode(if we're in edit mode we could set values on formcontrol)
    if (this.data.mode == 'add') {
      if (form != '') {
        formcontrols.forEach(e => {
          if (this.modalName === 'tenants' || this.modalName === "users") {
            this.currentFormDetail.push(e);
            this.form.addControl(e.formcontrolName, new FormControl('', e.validators));
          }

        })
      }
    }
    else {
      formcontrols.forEach(elem => {
        if (elem.formcontrolName !== "email" && elem.formcontrolName !== "randomPassword" && elem.formcontrolName !== "confirmPassword" && elem.formcontrolName !== "newPassword") {
          this.currentFormDetail.push(elem);
          this.form.addControl(elem.formcontrolName, new FormControl({ value: this.data.editables[elem.formcontrolName] != "null" ? this.data.editables[elem.formcontrolName] : '', disabled: elem.disabled }, elem.validators));
        }
      })
    }
  }
  closeModal() {
    if (this.modalName == 'tenants') {
      this.dialogRef.close({ event: "close" });
    }
    else {
      this.dialogRef.close();
    }
  }
  //for get a keys of arr of objects
  getKeys(data) {
    let val = keys(data);
    return val;
  }

  ngOnInit(): void {
    this.setInitialPermissions();
    if (this.page == 'super-users') {
      this.getSuperAdminRoles();
    }
    else {
      this.getRoles();
    }
    this.createForm();

  }


  setInitialPermissions(){
    const perm = Constants.PERMISSIONS;
    this.permissionsService.loadPermissions(perm);
    this.permissions = localStorage.getItem('permissions');
    let otherPermissions = this.permissions ? this.permissions : "";
    if(otherPermissions) {
      this.permissionsService.loadPermissions(
        JSON.parse(otherPermissions)
        .filter(_ => _.allow).map(_ => _.resource_access)
      );
    }
  }

  getPermission(permission, option=0) {
    let permissionList = this.permissions ? this.permissions : "";
    if(permissionList) {
      this.permissionsArray = JSON.parse(permissionList)
      .filter(_ => _.allow).map(_ => _.resource_access)
    }
    let final = this.permissionsArray.includes(permission);
    return final;
  }
}
