import { MatSnackBar } from '@angular/material/snack-bar';
import { DataService } from './../../services/data.service';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Component, OnInit } from '@angular/core';
import { Inject } from '@angular/core';
import { Optional } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
@Component({
  selector: 'app-add-input',
  templateUrl: './add-input.component.html',
  styleUrls: ['./add-input.component.scss']
})
export class AddInputComponent implements OnInit {

  isLoading = false;
  inputName = "";
  required = false;
  disabled = false;
  selectedInputName = "";
  selectedRequired = false;
  selectedDisabled = false;

  constructor(public dialogRef: MatDialogRef<AddInputComponent>, @Optional() @Inject(MAT_DIALOG_DATA) public data: any, private dialog: MatDialog, private dataServices: DataService, private _snackbar: MatSnackBar) { }

  ngOnInit(): void {
    if (this.data && this.data.type === 'edit') {
      this.isLoading = true;
      let {editItem}  = this.data;
      this.disabled = editItem.disabled;
      this.required = editItem.required;
      this.inputName = editItem.input_type_name;
      this.isLoading = false;

      this.selectedInputName = editItem.input_type_name;
      this.selectedRequired = editItem.required;
      this.selectedDisabled = editItem.disabled;
    }
  }

  getInputName(input) {
    this.inputName = input;
  }
  
  onCancelClick() {
    if (this.selectedInputName !=  this.inputName || this.selectedRequired != this.required || this.selectedDisabled != this.disabled ) {
      if (confirm("Are you sure want to cancel ")) {
        this.dialogRef.close({event:"close"});
      }
    }
    else {
      this.dialogRef.close({event:"close"});
    }
  }

  updateToggleStatus(event, type) {
    if(type == "disabled") {
      this.disabled = event;
    }
    else {
      this.required = event;
    }
  }

  closeModal()
  {
    this.dialogRef.close({event:"close"});
  }

  async createAPI() {
    // If dialog param is edit
    if (this.data && this.data.type === 'edit') {
      this.isLoading = true;
      let {editItem} = this.data;

      let inputObj = {
        "input_type_name": this.inputName,
        "required": this.required,
        "disabled": this.disabled
    }

      const res = await this.dataServices.updateInputType(inputObj, editItem.id);
      if (res.message) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
        })
      }
      this.isLoading = false;
      this.dialog.closeAll();
    }
    // If the dialog param is add
    else if (this.data && this.data.type === 'add') {
      this.isLoading = true;
      let inputObj = {
        "input_type_name": this.inputName,
        "required": this.required,
        "disabled": this.disabled
    }

      const res = await this.dataServices.postInputType(inputObj);
      if (res.message) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
          panelClass: ['redSnackBar'],
        })
      }
      this.isLoading = false;
      this.dialog.closeAll();
    }
  }

}
