import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AddInputComponent } from 'src/app/common/add-input/add-input.component';
import { Constants } from 'src/app/constants';
import { DataService } from '../../services/data.service';
import { AddApiComponent } from './../add-api/add-api.component';

@Component({
  selector: 'app-manage-inputs',
  templateUrl: './manage-inputs.component.html',
  styleUrls: ['./manage-inputs.component.scss']
})
export class ManageInputsComponent implements OnInit {

  tableData: any;
  tableColumns: any;
  tableColumnHeaders: any;
  menu: string = 'inputs';
  inputTypes: Array<String> = [];
  isLoading = false;
  apiData: any;
  failureMessage = '';
  skip = 0;
  limit = 10;
  page = 1;
  numberOfRecords = 0;
  constructor(private dataService: DataService, private dialog: MatDialog, private _snackbar: MatSnackBar) { }
  async ngOnInit(): Promise<void> {
    this.getInputs();
  }


  async getInputs() {
    this.isLoading = true;
    this.apiData = await this.dataService.getInputTypes(this.skip, this.limit);
    if (this.apiData.statusCode !== 200) {
      this.isLoading = false;
      this.failureMessage = this.apiData && this.apiData.message ? this.apiData.message : 'Something went Wrong!';
      return;
    }
    this.numberOfRecords = this.apiData.inputTypesCount;
    this.inputTypes = this.apiData;
    this.tableData = this.inputTypes['inputTypes'];
    this.tableColumns = Constants.MANAGE_INPUTS;
    this.tableColumnHeaders = Constants.MANAGE_INPUTS_HEADER;
    this.isLoading = false;

  }

  async onToggleClick(event) {
    this.isLoading = true;
    let editItem = event && event[4] || "";
    let inputObj = {
      "input_type_name": editItem && editItem.input_type_name || "",
      "required": editItem && editItem.required || "",
      "disabled": editItem && editItem.disabled || "",
    }

    const res = await this.dataService.updateInputType(inputObj, editItem.id);
    if (res.message) {
      this._snackbar.open(res.message, '', {
        duration: 2000,
      })
    }
    this.isLoading = false;
  }


  onCellClick(event) {
    // event -> table header, index, table data
    const currentData = event[2];
    if (event[0] === 'edit') {
      this.onEditClick(currentData);
    } else if (event[0] === 'delete') {
      this.onDeleteClick(currentData);
    }
  }
  onEditClick(apiData) {
    let editItem = apiData;
    this.dialog.open(AddInputComponent, {
      width: "640px",
      data: {
        type: 'edit',
        editItem,
      }
    }).afterClosed().subscribe((result) => {
      if (!result || result && result.event !== 'close') {
        this.getInputs();
      }
    });
  }
  async onDeleteClick(apiData) {
    let deleteItem = apiData;
    if (confirm("Are you sure want to delete " + deleteItem.id)) {
      const res = await this.dataService.deleteInputService(deleteItem.id);
      if (res.message) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
        })
      }
      this.getInputs();
    }
    // if (confirm("Are you sure to delete " + apiData.url)) {
    //   const res = await this.dataService.deleteApiService(apiData.id);
    //   if (res.message) {
    //     this._snackbar.open(res.message, '', {
    //       duration: 2000,
    //     })
    //   }
    //   this.getApi();
    // }
  }


  addNewApi() {
    this.dialog.open(AddInputComponent, {
      width: "640px",
      data: {
        type: 'add',
        apiData: null,
      }
    }).afterClosed().subscribe((result) => {
      if (!result || result && result.event !== 'close') {
        this.getInputs();
      }
    });
  }
  onPageChange(page) {
    this.page = page;
    this.skip = (page - 1) * this.limit;
    this.getInputs();
  }

}
