import { MatSnackBar } from '@angular/material/snack-bar';
import { DataService } from './../../services/data.service';
import { Component, OnInit } from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Inject } from '@angular/core';
import { Optional } from '@angular/core';


@Component({
  selector: 'app-surveytype-dialog',
  templateUrl: './surveytype-dialog.component.html',
  styleUrls: ['./surveytype-dialog.component.scss']
})
export class SurveytypeDialogComponent implements OnInit {
  surveyName: string = '';
  constructor(@Optional() @Inject(MAT_DIALOG_DATA) public data: any, private dialog: MatDialog, private dataService: DataService, private _snackbar: MatSnackBar) { }

  ngOnInit(): void {
    this.data && this.data.apiData ? this.surveyName = this.data.apiData.name : null;
  }
  onCancelClick() {
    this.dialog.closeAll();
  }
  onNameUpdate(event) {
    this.surveyName = event;
  }
  async onSaveClick() {
    if (this.surveyName && this.data.type === 'edit') {
      const res = await this.dataService.updateSurveyTypes(this.data.apiData.id, { name: this.surveyName });
      if (res.message) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
        })
      }
      this.onCancelClick();
    } else if (this.data.type === 'add') {
      const res = await this.dataService.createSurveyType({ name: this.surveyName, 'is_active': true });
      if (res.statusCode && res.statusCode === 201) {
        this._snackbar.open('Survey Created Successfully', '', {
          duration: 2000,
          panelClass: ['greenSnackBar']
        })
      } else {
        const msg = res.message ? res.message : 'Something went wrong';
        this._snackbar.open(msg, '', {
          duration: 2000,
          panelClass: ['redSnackBar']
        })
      }
      this.onCancelClick();
    }
  }
}
