import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { Constants } from '../../constants';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { UsersdialogComponent } from 'src/app/common/usersdialog/usersdialog.component';
import { MatDialog } from '@angular/material/dialog';
import { formatDate } from '@angular/common';
import { DataService } from 'src/app/services/data.service';
import { RolesandpermissionsComponent } from 'src/app/common/rolesandpermissions/rolesandpermissions.component';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-manage-super-users',
  templateUrl: './manage-super-users.component.html',
  styleUrls: ['./manage-super-users.component.scss']
})
export class ManageSuperUsersComponent implements OnInit {

  //for table header config
  tableData: any = [];
  tableColumns: any;
  tableColumnHeaders: any;
  skip = 0;
  limit = 10;
  page = 1;
  numberOfRecords = 0;
  isLoading = false;
  apiData: any;
  failureMessage = '';
  constructor(private modalDialog: MatDialog, private dataService: DataService, private dialog: MatDialog, private _snackbar: MatSnackBar) { }
  async ngOnInit(): Promise<void> {
    ;
    this.getUsers();
  }
  //for open a dialog for roles and permissions
  openRolesModal() {
    let dialogBoxSettings = {
      width: '500px',
      margin: '0 auto',
      "max-height": 'fit-content',
      disableClose: true,
      hasBackdrop: true,
      data: {
        page: "super-users",
      }
    }


    this.modalDialog.open(RolesandpermissionsComponent, dialogBoxSettings);
  }
  openUsersModal(modalname, mode, data?) {
    let dialogBoxSettings = {
      width: '500px',
      margin: '0 auto',
      "max-height": 'fit-content',
      disableClose: true,
      hasBackdrop: true,
      data: {
        "editables": data,
        "modalname": modalname,
        "mode": mode,
        "page": "super-users",
      }
    }
    this.modalDialog.open(UsersdialogComponent, dialogBoxSettings).afterClosed().subscribe((result) => {
      if (!result || result && result.event !== 'close') {
        this.getUsers();
      }
    });
  }
  //get users func
  async getUsers() {
    this.isLoading = true;
    this.apiData = await this.dataService.getSuperUsers(this.skip, this.limit);
    if (this.apiData.statusCode !== 200) {
      this.isLoading = false;
      this.failureMessage = this.apiData && this.apiData.message ? this.apiData.message : 'Something went Wrong!';
      return;
    }
    let users: any = [];
    this.apiData['users'].map(async e => {
      let tabledata = {};
      tabledata['user'] = {
        "name": e.username,
        "profile": e.profile_picture != null ? e.profile_picture : null,
        "userid": e.id,
        "firstname": e.first_name,
        "lastname": e.last_name != null ? e.last_name : '',
        "displayname": e.display_name != null ? e.display_name : null
      };
      tabledata['email'] = e.email;
      tabledata['created at'] = formatDate(e.created_at, 'dd MMM YYYY hh:mm a', 'en-US');
      tabledata['updated at'] = formatDate(e.updated_at, 'dd MMM YYYY hh:mm a', 'en-US');
      tabledata['role'] = e.role.name;
      // tabledata['created_by'] = {
      //   "name": e.created_by != null ? e.created_by.username : '',
      //   "profile": e.created_by != null ? e.created_by.profile_picture : '',
      // };
      tabledata['is_active'] = e.is_active;
      await users.push(tabledata);
    })
    this.tableData = await users;
    this.numberOfRecords = await this.apiData.usersCount;
    this.isLoading = false;
    this.tableColumns = Constants.MANAGE_SUPER_USERS_COLUMNS;
    this.tableColumnHeaders = Constants.MANAGE_SUPER_USERS_HEADERS;
  }
  onPageChange(page) {
    this.page = page;
    this.skip = (page - 1) * this.limit;
    this.getUsers();
  }

  //for edit & delete users
  onCellClick(event) {
    const currentData = event[2];
    if (event[0] === 'edit') {
      this.onEditClick(currentData);
    } else if (event[0] === 'delete') {
      this.onDeleteClick(currentData);
    }
  }
  onEditClick(apiData) {
    let editItem = {};
    editItem['username'] = apiData.user.name;
    editItem['first_name'] = apiData.user.firstname;
    editItem['last_name'] = apiData.user.lastname;
    editItem['display_name'] = apiData.user.displayname;
    editItem['profile'] = apiData.user.profile;
    editItem['role_id'] = apiData.role;
    editItem['email'] = apiData.email;
    editItem['userid'] = apiData.user.userid;
    editItem['is_active'] = apiData.is_active;
    editItem['reset_password'] = "";
    this.openUsersModal('usersmodal', 'edit', editItem);
    // this.dialog.open(AddInputComponent, {
    //   width: "640px",
    //   data: {
    //     type: 'edit',
    //     editItem,
    //   }
    // }).afterClosed().subscribe((result) => {
    //   if (!result || result && result.event !== 'close') {
    //     this.getUsers();
    //   }
    // });
  }
  async onDeleteClick(apiData) {
    let deleteItem = apiData;
    if (confirm("Are you sure want to delete " + deleteItem.id)) {
      const res = await this.dataService.deleteInputService(deleteItem.id);
      if (res.message) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
        })
      }
      this.getUsers();
    }
  }
}
