  // Survey History
  getSurveyHistory(surveyTypeId: number, limit: any, skip: any, search: any, completed: any, stopped: any, locked: any, interrupted: any, saved: any, uploaded: any, filterUploadedToCRM: any, dateCreated: any, dateModified: any, sortColumn: any, sortType: any) {
    let params = {
      skip, limit, search
    };

    if (completed !== null) params['completed'] = completed;
    if (stopped !== null) params['stopped'] = stopped;
    if (locked !== null) params['locked'] = locked;
    if (saved !== null) params['saved'] = saved;
    if (interrupted !== null) params['interrupted'] = interrupted;
    if (uploaded !== null) params['has_video'] = uploaded;
    if (filterUploadedToCRM !== null) params['upload_to_crm'] = filterUploadedToCRM;
    if (dateCreated.length > 0) params['dateCreated'] = dateCreated;
    if (dateModified.length > 0) params['dateModified'] = dateModified;
    if(sortColumn) params['sort_column'] = sortColumn;
    if(sortType) params['sort_type'] = sortType; 
    const url = `${this.baseUrl}/admin/${surveyTypeId}/surveys`;
    const token = this.getCookie('token');
    const options = new HttpHeaders({
      Authorization: `Bearer ${token}`
    })
    return this.http.get(url, { headers: options, params: params })
      .toPromise()
      .then((response) => {
        return response;
      })
      .catch((err) => err);
  }

