import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxPermissionsService } from 'ngx-permissions';
import { Constants } from 'src/app/constants';

@Component({
  selector: 'app-settings-page',
  templateUrl: './settings-page.component.html',
  styleUrls: ['./settings-page.component.scss']
})
export class SettingsPageComponent implements OnInit {

  index = 0;
  initialTabIndex = 0;
  permissions: string | null = "";
  permissionsArray: Array<String> = [];

  constructor(private router: Router, private activatedRoute: ActivatedRoute, private permissionsService: NgxPermissionsService) { }

  ngOnInit(): void {
  const perm = Constants.PERMISSIONS;
  this.permissionsService.loadPermissions(perm);    
  this.permissions = localStorage.getItem('permissions');
  let settingsPermission = this.permissions ? this.permissions : "";
  if(settingsPermission) {
    this.permissionsService.loadPermissions(
      JSON.parse(settingsPermission)
      .filter(_ => _.allow).map(_ => _.resource_access)
    );
  }
  this.checkAndSetInitialIndex();
  }

  navigateToIndex(index) {
    this.router.navigate([], {
     queryParams: {
       "settings-tab": index
     },
     queryParamsHandling: 'merge',
   });
  }

  getPermission(permission, option=0) {
    let permissionList = this.permissions ? this.permissions : "";
    if(permissionList) {
      this.permissionsArray = JSON.parse(permissionList)
      .filter(_ => _.allow).map(_ => _.resource_access)
    }
    let final = this.permissionsArray.includes(permission);
    return final;
  }

  checkAndSetInitialIndex() {
    this.activatedRoute.queryParams.subscribe(params => {
      let tab = params['settings-tab'];
      if(tab) {
        this.index = tab;
        this.navigateToIndex(tab);
      }
      else {
        // inital route - API services tab
        let initialTab = 0;
        if(this.getPermission('manage_api_services')) {
          initialTab = 0;
        }
        else if(!this.getPermission('manage_api_services') && this.getPermission('manage_survey_types')) {
          initialTab = 1;
        }
        else if(!this.getPermission('manage_api_services') && !this.getPermission('manage_survey_types') && this.getPermission('manage_users')) {
          initialTab = 2;
        }
        else if(!this.getPermission('manage_api_services') && !this.getPermission('manage_survey_types') && !this.getPermission('manage_users') && this.getPermission('manage_customer_api')) {
          initialTab = 3;
        }
        else if(!this.getPermission('manage_api_services') && !this.getPermission('manage_survey_types') && !this.getPermission('manage_users') && !this.getPermission('manage_customer_api') && this.getPermission('manage_answer_fields')) {
          initialTab = 4;
        }
        this.index = this.initialTabIndex;
        this.navigateToIndex(this.initialTabIndex);
      }
  });
  }
  
  setIndex(index) {
    this.index = index;
    this.navigateToIndex(index);
  }
  tabSelect(event) {
    let {index} = event;
    this.setIndex(index);
  }

}
