import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DataService } from '../../services/data.service';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import {Constants} from '../../constants';
import { strict } from 'assert';

let { COOKIE_OPTIONS: {EXPIRE_DAYS, PATH, DOMAIN, SECURE_FLAG, SAME_SITE} } = Constants;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  constructor(private cookieService: CookieService, private _snackBar: MatSnackBar, private dataService: DataService, private router: Router) { }
  email = "";
  password = "";
  buttonDisabled = false;
  showPassword = false;
  isSuperAdmin = this.router.url.includes('super-admin');
  ngOnInit(): void {
    if (this.dataService.getCookie('token')) {
      this.isSuperAdmin ? this.router.navigate(['super-admin/home']) : this.router.navigate(['questions']);
    }
  }
  getEmail(email: string) {
    this.email = email;
  }
  getPassword(password: string) {
    this.password = password;
  }
  navigate(path: string) {
    this.router.navigate([`/${path}`]);
  }
  onEnterClick() {
    this.onLoginClick();
  }

  async onLoginClick() {
    this.buttonDisabled = true;
    // Removed validation for email, since email is been changed to user name
    // Validation for email
    // if (/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(this.email)) {
    try {

      const res = !this.isSuperAdmin ? await this.dataService.login(this.email, this.password) : await this.dataService.superAdminLogin(this.email, this.password);
      if (res.statusCode === 200) {
        if (res.message && res.token) {
          if (!this.isSuperAdmin) {
            this.dataService.setCookie('token', res.token.token);
            localStorage.setItem('userId', res.userInfo && res.userInfo.id ? res.userInfo.id : '');
            localStorage.setItem('permissions', res.userInfo && res.userInfo.role && res.userInfo.role.permissions ? JSON.stringify(res.userInfo.role.permissions) : "");
            localStorage.setItem('firstName', res.userInfo.display_name ? res.userInfo.display_name : res.userInfo.first_name);
            localStorage.setItem('avatar', res.userInfo.profile_picture ? res.userInfo.profile_picture : '');
            localStorage.setItem('logginedAs', 'admin');
          } else if (this.isSuperAdmin) {
            this.dataService.setCookie('superAdminToken', res.token.token);
            localStorage.setItem('userId', res.userInfo && res.userInfo.id ? res.userInfo.id : '');
            localStorage.setItem('permissions', res.userInfo && res.userInfo.role && res.userInfo.role.permissions ? JSON.stringify(res.userInfo.role.permissions) : "");
            // localStorage.setItem('firstName', `${res.userInfo.first_name} ${res.userInfo.last_name ? res.userInfo.last_name : ''}`);
            localStorage.setItem('superAdminFirstName', res.userInfo.display_name ? res.userInfo.display_name : res.userInfo.first_name);
            localStorage.setItem('superAdminAvatar', res.userInfo.profile_picture ? res.userInfo.profile_picture : '');
            localStorage.setItem('logginedAs', 'superadmin');
          }
          this._snackBar.open(res.message, '', {
            duration: 2000,
            panelClass: ['greenSnackBar'],
          });
          const loggedinAs = localStorage.getItem('logginedAs');
          if (loggedinAs === 'admin' && this.dataService.getCookie('token')) {
            this.router.navigate(['questions']);
          } else if (loggedinAs === 'superadmin' && this.dataService.getCookie('superAdminToken')) {
            this.router.navigate(['super-admin/home']);
          }
        }
      }
      else {
        let errorMsg;
        if (res.error && res.error.message) {
          errorMsg = res.error.message;
        } else {
          errorMsg = 'Something went wrong!';
        }
        this._snackBar.open(errorMsg, '', {
          duration: 2000,
          panelClass: ['redSnackBar'],
        });
        this.buttonDisabled = false;
      }
    } catch (err) {
      this._snackBar.open(err, '', {
        duration: 2000,
        panelClass: ['redSnackBar'],
      });
      console.error("Login Error: ", err);
      this.buttonDisabled = false;
    }
  }
  // else {
  //   this._snackBar.open('Invalid Email', '', {
  //     duration: 2000,
  //     panelClass: ['redSnackBar'],
  //   });
  // }
  // this.buttonDisabled = false;
  // }
  togglePassword(event: any) {
    this.showPassword = !this.showPassword;
  }
  switchLogins() {
    if (this.isSuperAdmin) {
      this.router.navigate(['/']);
    } else {
      this.router.navigate(['/super-admin']);
    }
  }
}
