import {
  Component,
  Input,
  OnInit,
  Output,
  EventEmitter,
  NgZone,
  ViewChild,
} from '@angular/core';
import { CdkTextareaAutosize } from '@angular/cdk/text-field';
import { take } from 'rxjs/operators';
import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer } from "@angular/platform-browser";

const logoURL = 
"https://raw.githubusercontent.com/fireflysemantics/logo/master/l1.svg";


const passwordIcon = "../../../assets/new-design-icons/password-icon.svg";

const emailIcon = "../../../assets/new-design-icons/email-icon.svg";

const filterIcon = "../../../assets/new-design-icons/Filter-icon.svg";

const searchIcon = "../../../assets/new-design-icons/search-icon.svg";

@Component({
  selector: 'app-npec-text-field',
  templateUrl: './npec-text-field.component.html',
  styleUrls: ['./npec-text-field.component.scss']
})
export class NpecTextFieldComponent implements OnInit {
  @Input() label = '';
  @Input() inputType = 'string';
  @Input() type = 'input';
  @Input() placeholder = '';
  @Input() value = '';
  @Input() isOutline = true;
  @Input() name = "";
 
  @Input() appendIcon = '';
  @Input() prependIcon = '';
  @Input() customIcon = false;
  @Input() customImgValue = '';
 
  @Input() disabled = false;
  @Input() isLoading = true;

  

  @Input() appendButton = false;
  @Input() appendButtonDisabled = false;

  @Output() onClick = new EventEmitter<string>();
  @Output() onChange = new EventEmitter<string>();
  @Output() onEnter = new EventEmitter<KeyboardEvent>();
  @Output() onIconClick = new EventEmitter<MouseEvent>();
  constructor(private _ngZone: NgZone,private matIconRegistry: MatIconRegistry,
    private domSanitizer: DomSanitizer) {
      this.matIconRegistry.addSvgIcon(
        "logo",
        this.domSanitizer.bypassSecurityTrustResourceUrl(logoURL));

      this.matIconRegistry.addSvgIcon(
        "passwordIcon",
        this.domSanitizer.bypassSecurityTrustResourceUrl(passwordIcon));

      this.matIconRegistry.addSvgIcon(
        "emailIcon",
        this.domSanitizer.bypassSecurityTrustResourceUrl(emailIcon));

      this.matIconRegistry.addSvgIcon(
        "filterIcon",
        this.domSanitizer.bypassSecurityTrustResourceUrl(filterIcon));
        
      this.matIconRegistry.addSvgIcon(
        "searchIcon",
        this.domSanitizer.bypassSecurityTrustResourceUrl(searchIcon));
     }
  @ViewChild('autosize') autosize!: CdkTextareaAutosize;
  ngOnInit(): void { }
  onTextValueChange(event: any, type: any) {
    this.onChange.emit(event.target.value);
  }
  onEnterClick(event: any) {
    this.onEnter.emit(event);
  }
  triggerResize() {
    this._ngZone.onStable
      .pipe(take(1))
      .subscribe(() => this.autosize.resizeToFitContent(true));
  }
  appendIconClick(event: any) {
    this.onIconClick.emit(event.target.value);
    event.stopPropagation();
  }

  appendClick() {
    if(!this.appendButtonDisabled) {
      this.onClick.emit();
    }
  }
}
