import { Component, OnInit } from '@angular/core';
// Session Timeout
import { BnNgIdleService } from 'bn-ng-idle';

import { MatDialog } from '@angular/material/dialog';
import { DataService } from  './services/data.service';
import { DomSanitizer } from '@angular/platform-browser';
import { CookieService } from 'ngx-cookie-service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Constants } from './constants';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  title = 'NPEC-Admin';
  expiredTime = Constants.SESSION_EXPIRED_TIME;

  constructor(private bnIdle: BnNgIdleService, private cookieService: CookieService, private modalDialog: MatDialog, private dataService: DataService, private domSanitizer: DomSanitizer, private router: Router, private _snackbar: MatSnackBar) {
    let logginedAs = localStorage.getItem('logginedAs');
    if (logginedAs === 'admin') {
      if (!this.dataService.getCookie('token')) {
        this.logout();
      }
    }
  } 
  ngOnInit(): void {
    this.bnIdle.startWatching(this.expiredTime).subscribe((isTimedOut: boolean) => {
        this.logout();
    });
  }
  showLogoutSnackbar() {
    this._snackbar.open('Session Expired... Logged out', '', {
      duration: 2000,
      panelClass: ['greenSnackBar'],
    })
  }
  logout() {
    let logginedAs = localStorage.getItem('logginedAs');
    if (logginedAs === 'admin') {
      localStorage.clear();
      this.dataService.deleteAllCookies();
      this.showLogoutSnackbar();
      this.router.navigate(['/']);
    } else if (logginedAs === 'superadmin') {
      localStorage.clear();
      this.dataService.deleteAllCookies();
      this.showLogoutSnackbar();
      this.router.navigate(['/super-admin']);
    }
    else {
      // Already Logged out
    }
  }
}
