import { ManageTenentsComponent } from './pages/manage-tenents/manage-tenents.component';
import { ResetPasswordComponent } from './pages/reset-password/reset-password.component';
import { HomePageComponent } from './pages/home-page/home-page.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ForgotPasswordComponent } from './pages/forgot-password/forgot-password.component';
import { HomeComponent } from './pages/home/home.component';
import { LoginComponent } from './pages/login/login.component';
import { SuperAdminHomePageComponent } from './pages/super-admin-home-page/super-admin-home-page.component';
import { VideoPageComponent } from './pages/video-page/video-page/video-page.component';
import { SuperAdministratorComponent } from './pages/super-administrator/super-administrator.component';

const routes: Routes = [
  { path: '', component: LoginComponent },
  { path: 'super-admin', component: LoginComponent },
  { path: 'forgot-password', component: ForgotPasswordComponent },
  { path: 'super-admin/forgot-password', component: ForgotPasswordComponent },
  { path: 'super-admin/reset-password', component: ResetPasswordComponent },
  { path: 'questions', component: HomePageComponent },
  { path: 'reset-password', component: ResetPasswordComponent },
  { path: 'questions/video', component: VideoPageComponent },
  { path: 'super-admin/home', component: SuperAdministratorComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
