import { Component, OnInit, Input } from '@angular/core';
import { MatSlideToggleChange } from '@angular/material/slide-toggle';
import { DataService } from 'src/app/services/data.service';
import { Constants } from '../../constants';
import { Inject } from '@angular/core';
import { Optional } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';


@Component({
  selector: 'rolesandpermissions',
  templateUrl: './rolesandpermissions.component.html',
  styleUrls: ['./rolesandpermissions.component.scss']
})
export class RolesandpermissionsComponent implements OnInit {
  role:string='';
  roles:any=[];
  selectedRole:string='';
  @Input() page: string = 'admin';
  RolesModules=Constants.ROLESMODULES;
  rolePermissions:any=[];
  rolesDropdownObj: any = [];
  permissions:any = [];
  loader:boolean=false;
  //loader for while add role
  addRoleLoader:boolean=false;
  constructor(public dialogRef: MatDialogRef<RolesandpermissionsComponent>,private apiservice:DataService,  @Optional() @Inject(MAT_DIALOG_DATA) public data: any) { 
  }
  
  ngOnInit(): void {
    if(this.data && this.data.page && this.data.page === 'super-users') {
      this.page = this.data.page;
      this.getSuperUserRoles();
    }
    else {
      this.getRoles();
    }
  }
  async getRolePermission(selectedrole)
  {
    this.loader=true;
    this.apiservice.getSingleRole(parseInt(selectedrole)).subscribe(async res=>{
      if(res.statusCode==200)
      {
      this.loader = await false;  
      this.rolePermissions= await res['role']['permission'];
      console.log("rol=====>", this.rolePermissions);
      }
    });
  }

  getRolesAndPermissions(role) {
    let roleId = role.id;
    this.selectedRole = roleId;
    if(this.page === 'super-users') {
      this.getSuperAdminRolePermission(roleId)
    }
    else {
      this.getRolePermission(roleId)
    }
  }

  async getSuperAdminRolePermission(selectedrole)
  {
    this.loader=true;
    let res = await this.apiservice.getSuperAdminSingleRole(parseInt(selectedrole));
      if(res.statusCode==200)
      {
      this.loader = await false;  
      this.rolePermissions= await res['role']['permission'];
      }
      else {
        this.loader = false;
      }
  }


  async getSuperAdminSingleRole(selectedrole) {
    this.loader=true;
    let res  = await this.apiservice.getSuperAdminSingleRole(parseInt(selectedrole))
      if(res.statusCode==200)
      {
      this.loader = await false;  
      this.rolePermissions= await res['role']['permission'];
      }
  }

//To edit role
async editRole()
{
  this.addRoleLoader=true
  let roleData={
    "name":this.role
  }
  this.apiservice.editRole(this.selectedRole['id'],roleData).subscribe(async res=>{
    if(res.statusCode==200)
    {
      this.addRoleLoader=await false;
     await this.getRoles();
    }
  })
}
  //To get all admin roles
  async getRoles()
  {
    this.apiservice.getRoles().subscribe(async res=>{
      this.roles = await res.roles;
      this.selectedRole= await this.roles[0].id;

      this.rolesDropdownObj = this.roles.map((data) => {
        data['label']= data.name || '';
        data['value']= data.id || '';
        return data;
      })
      await this.getRolePermission(this.selectedRole);
    });
   
  }

   //To get all surper admin oles
   async getSuperUserRoles()
   {
    
    let apiData = await this.apiservice.getSuperUserRoles();
    let {roles} = apiData;
    this.roles = roles;
    this.selectedRole= await this.roles[0].id;
    await this.getSuperAdminRolePermission(this.selectedRole);
   }


   addAdminOrSuperAdminRoles() {
    if(this.page === 'super-users') {
      this.addSuperAdminRoles();
    }
    else {
      this.addRoles();
    }
   }
   
  async addRoles()
  {
    this.addRoleLoader=true;
    if(this.role!='')
    {
      let roleData={
        "name":this.role
      }
     this.apiservice.addRole(roleData).subscribe(async res=>{
       if(res.statusCode==201)
       {
         this.role= '';
         this.addRoleLoader=await false;
         await this.getRoles();
       }
     });
    }
  }

  async addSuperAdminRoles()
  {
    this.addRoleLoader=true;
    if(this.role!='')
    {
      let roleData={
        "name":this.role
      }
     this.apiservice.addSuperAdminRole(roleData).subscribe(async res=>{
       if(res.statusCode==201)
       {
         this.role= '';
         this.addRoleLoader=await false;
         await this.getSuperUserRoles();
       }
     });
    }
  }

  toggleStatus($event) {
    console.log("value====>", $event);
  }

  //for change a permisson status
  changeStatus(permissionindex,state:MatSlideToggleChange)
  {   
    let roleData = {
      "allow":state.checked
    }
    // roleData.append("allow",state.checked.toString());

    if(this.page === 'super-users') {
      this.apiservice.updateSuperUserPermissions(roleData,permissionindex).subscribe(res=>{
      })
    }
    else {
      this.apiservice.updatePermissions(roleData,permissionindex).subscribe(res=>{
      })
    }
  }

  closeModal()
  {
    this.dialogRef.close();
  }

  setRoleName(name) {
  this.role = name;
  }
}
