import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Component, Inject, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-profile-confirm-modal',
  templateUrl: './profile-confirm-modal.component.html',
  styleUrls: ['./profile-confirm-modal.component.scss']
})
export class ProfileConfirmModalComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<ProfileConfirmModalComponent>, private modalDialog: MatDialog, @Inject(MAT_DIALOG_DATA) public data: any) {
  
  }

  ngOnInit(): void {
  }

  removeImage() {
    this.dialogRef.close({ event: "remove" });
  }
  changeImage() {
    this.dialogRef.close({ event: "change" });
  }
  closeModal() {
    this.dialogRef.close({ event: "close" });
  }
}
