import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Component, Input, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  constructor(private router: Router, private _snackbar: MatSnackBar, private dataService: DataService) { }

  ngOnInit(): void {
    if (localStorage.getItem('logginedAs') === 'admin' && this.dataService.getCookie('superAdminToken')) {
      this.showSuperAdmin = true;
    }
  }
  @Input() user: string = '';
  @Input() image: string = '';
  @Input() logo: string = '';
  @Input() orgName: string = '';
  showSuperAdmin: boolean = false;
  logout() {
    let logginedAs = localStorage.getItem('logginedAs');
    if (logginedAs === 'admin') {
      localStorage.clear();
      this.dataService.deleteAllCookies();
      this.router.navigate(['/']);
    } else if (logginedAs === 'superadmin') {
      localStorage.clear();
      this.dataService.deleteAllCookies();
      this.router.navigate(['/super-admin']);
    }
    this._snackbar.open('Logged out successfully', '', {
      duration: 2000,
      panelClass: ['greenSnackBar'],
    })
  }
  goToSuperAdmin() {
    // Functionality to go back to super admin, if logged in from super admin
    this.router.navigate(['super-admin/home']);
    localStorage.setItem('logginedAs', 'superadmin');
  }
}
