import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';

@Component({
  selector: 'app-draggable',
  templateUrl: './draggable.component.html',
  styleUrls: ['./draggable.component.scss'],
})
export class DraggableComponent implements OnInit {
  @Input() dataSet: any = [];
  @Output() onChange = new EventEmitter<any>();
  @Output() onEditQuestion = new EventEmitter<any>();
  public localData = [
    {
      id: '',
      question: '',
      number: '',
      suffix: '',
      order: 0,
      is_popup_question: false,
      is_active: false,
    },
  ];
  constructor() { }

  ngOnInit(): void {
    this.localData = this.dataSet;
  }
  ngOnChanges(event: any): void {
    if (event && event.dataSet && event.dataSet.currentValue) {
      this.localData = [...event.dataSet.currentValue];
    }
  }
  async clickEvent(event: CdkDragDrop<string[]>) {
    await moveItemInArray(
      this.localData,
      event.previousIndex,
      event.currentIndex
    );
    // let data = this.localData.map((item: any, i: number) => {
    //   return { ...item, number: `${i + 1}` };
    // });
    // this.localData = [...data];
    // this.onChange.emit(this.localData);
    this.onChange.emit(Number(event.currentIndex) + 2)
  }
  onEdit(event: any, item: any) {
    event.stopPropagation();
    this.onEditQuestion.emit(item);
  }
}
