import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/services/data.service';

import { NgxPermissionsService } from 'ngx-permissions';
import { ActivatedRoute, Router } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-super-administrator',
  templateUrl: './super-administrator.component.html',
  styleUrls: ['./super-administrator.component.scss']
})
export class SuperAdministratorComponent implements OnInit {

  selectedOption = 0;
  index = 0;
  initialTabIndex = 0;
  orgName: string = "";
  logo: string = "";
  userName = localStorage.getItem('superAdminFirstName');
  profileImage = localStorage.getItem('superAdminAvatar');

  
  constructor(private router: Router, private activatedRoute: ActivatedRoute, private permissionsService: NgxPermissionsService, private dataService: DataService, private modalDialog: MatDialog, private _snackbar: MatSnackBar) { }


  ngOnInit(): void {
    this.checkAndSetInitialIndex();
    const loggedinAs = localStorage.getItem('logginedAs');
    if (loggedinAs === 'superadmin' && this.dataService.getCookie('superAdminToken')) {
      this.orgName = "Call Pilot";
      this.logo = "call-pilot-logo";
    }
  }

  navigateToIndex(index){
    this.router.navigate([], {
     queryParams: {
       tab: index
     },
     queryParamsHandling: 'merge',
   });
  }

  checkAndSetInitialIndex() {
    this.activatedRoute.queryParams.subscribe(params => {
      let tab = params['tab'];
      if(tab) {
        this.index = tab;
        this.navigateToIndex(tab);
      }
      else {
        // inital route - tenanats tab
        this.index = this.initialTabIndex;
        this.navigateToIndex(this.initialTabIndex);
      }
  });
  }
  
  setIndex(index) {
    this.index = index;
    this.navigateToIndex(index);
  }
  tabSelect(event) {
    let {index} = event;
    this.setIndex(index);
  }

  logout() {
    let logginedAs = localStorage.getItem('logginedAs');
    if (logginedAs === 'admin') {
      localStorage.clear();
      this.dataService.deleteAllCookies();
      this.router.navigate(['/']);
    } else if (logginedAs === 'superadmin') {
      localStorage.clear();
      this.dataService.deleteAllCookies();
      this.router.navigate(['/super-admin']);
    }
    this._snackbar.open('Logged out successfully', '', {
      duration: 2000,
      panelClass: ['greenSnackBar'],
    })
  }

}
