import { SurveytypeDialogComponent } from './../../common/surveytype-dialog/surveytype-dialog.component';
import { DataService } from './../../services/data.service';
import { Constants } from 'src/app/constants';
import { Component, OnInit } from '@angular/core';
import { formatDate } from '@angular/common';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-survey-types',
  templateUrl: './survey-types.component.html',
  styleUrls: ['./survey-types.component.scss']
})
export class SurveyTypesComponent implements OnInit {

  constructor(private dataService: DataService, private _snackbar: MatSnackBar, private dialog: MatDialog) { }
  tableColumns = Constants.TABLE_COLUMNS.SURVEY_TYPES;
  tableColumnHeaders = Constants.TABLE_COLUMNS.SURVEY_TYPES_HEADER;
  tableData = [];

  skip = 0;
  limit = 10;
  apiResponse: any;
  isLoading = false;
  failureMessage = '';
  totalCount = 0;
  currentPage = 1;
  ngOnInit(): void {
    this.getQuestions();
  }
  async getQuestions() {
    this.isLoading = true;
    this.apiResponse = await this.dataService.getSurveys(this.skip, this.limit);
    if (this.apiResponse.statusCode && this.apiResponse.statusCode === 200) {
      this.totalCount = this.apiResponse.surveyTypesCount;
      const tableArray: any = [];
      this.apiResponse.surveyTypes.forEach(element => {
        let tableData: any = {};
        tableData['name'] = element.name;
        tableData['disabled'] = element.is_active;
        tableData['survey_id'] = element.id;
        tableData['created_at'] = formatDate(element.created_at, 'dd MMM YYYY hh:mm a', 'en-US');
        tableData['updated_at'] = formatDate(element.updated_at, 'dd MMM YYYY hh:mm a', 'en-US');
        tableArray.push(tableData);
      });
      this.tableData = tableArray;
    } else {
      const message = this.apiResponse.message ? this.apiResponse.message : 'Something went wrong';
      this.failureMessage = message;
    }
    this.isLoading = false;
  }
  async updateSurveyDisabled(event) {
    const res = await this.dataService.updateSurveyTypes(this.apiResponse.surveyTypes[event[1]].id, { 'is_active': event[0] });
    if (res.message) {
      this._snackbar.open(res.message, '', {
        duration: 2000,
      })
    }
    this.getQuestions();
  }
  cellClicked(event) {
    if (event[0] === 'edit') {
      this.dialog.open(SurveytypeDialogComponent, { width: '640px', data: { type: 'edit', apiData: this.apiResponse.surveyTypes[event[1]] } }).afterClosed().subscribe(() => this.getQuestions());
    }
  }
  onPageChange(page) {
    this.currentPage = page;
    this.skip = (page - 1) * this.limit;
    this.getQuestions();
  }
  onAddSurvey() {
    this.dialog.open(SurveytypeDialogComponent, { width: '640px', data: { type: 'add' } }).afterClosed().subscribe(() => this.getQuestions());
  }
}
