import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';

@Component({
  selector: 'app-super-admin-home-page',
  templateUrl: './super-admin-home-page.component.html',
  styleUrls: ['./super-admin-home-page.component.scss']
})
export class SuperAdminHomePageComponent implements OnInit {

  selectedOption = 0;
  index = 0;
  orgName: string = "";
  logo: string = "";
  userName = localStorage.getItem('superAdminFirstName');
  profileImage = localStorage.getItem('superAdminAvatar');

  constructor(private dataService: DataService) { }

  ngOnInit(): void {
    const loggedinAs = localStorage.getItem('logginedAs');
    if (loggedinAs === 'superadmin' && this.dataService.getCookie('superAdminToken')) {
      this.orgName = "Call Pilot";
      this.logo = "call-pilot-logo";
    }
  }

  optionClicked(option: number) {
    this.selectedOption = option;
  }
  onIndexChange(ind: number) {
    this.index = ind;
  }

}
