import { MatSnackBar } from '@angular/material/snack-bar';
import { DataService } from './../../services/data.service';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Inject } from '@angular/core';
import { Optional } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-tenant-auth-modal',
  templateUrl: './tenant-auth-modal.component.html',
  styleUrls: ['./tenant-auth-modal.component.scss']
})
export class TenantAuthModalComponent implements OnInit {

  isLoading = false;
  logo: any = "";
  orgId: String = "";
  organizationName: String = "";
  apiData: any = "";
  failureMessage = '';
  orgAdmin:any = "";
  adminName: String = "";
  adminProfileImg: any = "";
  userId: string = "";
  isUserDataLoaded = false;

  constructor(private router: Router, private dataService: DataService,public dialogRef: MatDialogRef<TenantAuthModalComponent>, @Optional() @Inject(MAT_DIALOG_DATA) public data: any, private dialog: MatDialog, private dataServices: DataService, private _snackbar: MatSnackBar) { }

  ngOnInit(): void {
   let {selectedItem} = this.data;
   this.orgId = selectedItem.id + "";
   if(selectedItem && selectedItem.user) {
    let {user} = selectedItem;
    this.logo = user.profile;
    this.organizationName = user.name;

   }
   this.getUsers();
  }

  async getUsers() {
    this.isLoading = true;
    const userDetails = await this.dataService.getTenantUsers(this.orgId);
    let {users} = userDetails;
    this.orgAdmin = users ? users[0] : {};
    const {username, profile_picture, id} = this.orgAdmin;
    if(this.orgAdmin) {
      this.adminName = username;
      this.adminProfileImg = profile_picture;
      this.userId = id;
      this.isUserDataLoaded = true;
      this.isLoading = false;
    }
  }

  onCancelClick() {
    this.dialogRef.close({event:"close"});
  }

  async loginAsTenant() {
    this.isLoading = true;
    this.apiData = await this.dataService.loginAsSuperAdmin(this.orgId, this.userId);
    if(this.apiData.statusCode === 200) {
      this.dataServices.setCookie('token', this.apiData.token.token);
      localStorage.setItem('firstName', this.apiData.user.display_name ? this.apiData.user.display_name : this.apiData.user.first_name);
      localStorage.setItem('avatar', this.apiData.user.profile_picture ? this.apiData.user.profile_picture : '');
      localStorage.setItem('permissions', this.apiData && this.apiData.user && this.apiData.user.role && this.apiData.user.role.permissions ? JSON.stringify(this.apiData.user.role.permissions) : "");
      localStorage.setItem('logginedAs', 'admin');
      this.isLoading = false;
      this.router.navigate(['/questions']);
      this.dialogRef.close({event:"login"});
    }
    else {
      this.isLoading = false;
      this.failureMessage = this.apiData && this.apiData.message ? this.apiData.message : 'Something went Wrong!';
      this.dialogRef.close({event:"login"});
      return;
    }
  }
}
