import { MatSnackBar } from '@angular/material/snack-bar';
import { AnswerfieldDialogComponent } from './../../common/answerfield-dialog/answerfield-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { DataService } from './../../services/data.service';
import { Constants } from 'src/app/constants';
import { Component, OnInit } from '@angular/core';
import { formatDate } from '@angular/common';

@Component({
  selector: 'app-answerfield',
  templateUrl: './answerfield.component.html',
  styleUrls: ['./answerfield.component.scss']
})
export class AnswerfieldComponent implements OnInit {
  isLoading = false;
  failureMessage = '';
  tableColumns = Constants.TABLE_COLUMNS.ANSWER_FIELD;
  tableColumnHeaders = Constants.TABLE_COLUMNS.ANSWER_FIELD_HEADER;
  tableData = [];
  apiData: any;
  currentPage = 1;
  skip = 0;
  limit = 10;
  numberOfRecords = 0;
  constructor(private dataService: DataService, private dialog: MatDialog, private _snackbar: MatSnackBar) { }

  ngOnInit(): void {
    this.getAnswerFields();
  }
  async getAnswerFields() {
    this.isLoading = true;
    this.apiData = await this.dataService.getAnswerFields(this.skip, this.limit);
    if (this.apiData.statusCode === 200) {
      this.numberOfRecords = this.apiData.answerFieldsCount;
      const tableArray: any = [];
      if (this.apiData.answerFields) {

        this.apiData.answerFields.forEach(element => {
          let tableData: any = {}
          tableData['label'] = element.label;
          tableData['field'] = element.field;
          tableData['created_at'] = formatDate(element.created_at, 'dd MMM YYYY hh:mm a', 'en-US');
          tableData['updated_at'] = formatDate(element.updated_at, 'dd MMM YYYY hh:mm a', 'en-US');
          tableArray.push(tableData);
        });
      }
      this.tableData = tableArray;
    } else if (this.apiData.message) {
      this.failureMessage = this.apiData.message;
    }
    this.isLoading = false;
  }
  async onCellClick(event) {
    if (event[0] === 'edit') {
      this.dialog.open(AnswerfieldDialogComponent, {
        width: '640px',
        data: { type: 'edit', value: this.apiData.answerFields[event[1]] }
      }).afterClosed().subscribe(() => this.getAnswerFields());
    } else if (event[0] === 'delete') {
      if (confirm("Are you sure want to delete" + this.apiData.answerFields[event[1]].label)) {
        const res = await this.dataService.deleteAnswerFields(this.apiData.answerFields[event[1]].id)
        if (res.message) {
          this._snackbar.open(res.message, '', {
            duration: 2000,
          })
        }
        this.getAnswerFields()
      }
    }
  }
  addAnswerField() {
    this.dialog.open(AnswerfieldDialogComponent, {
      width: '640px',
      data: { type: 'add' }
    }).afterClosed().subscribe(() => this.getAnswerFields());
  }
  onPageChange(page) {
    this.currentPage = page;
    this.skip = (page - 1) * this.limit;
    this.getAnswerFields();
  }
}
