import { Component, OnInit } from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Inject } from '@angular/core';
import { DataService } from 'src/app/services/data.service';
import { Constants } from 'src/app/constants';
import lodash from 'lodash';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-survey-history-dialog',
  templateUrl: './survey-history-dialog.component.html',
  styleUrls: ['./survey-history-dialog.component.scss']
})
export class SurveyHistoryDialogComponent implements OnInit {
  prev_url: any;
  constructor(private sanitizer: DomSanitizer, private dataService: DataService, @Inject(MAT_DIALOG_DATA) public data: any, private dialog: MatDialog, private _snackbar: MatSnackBar) {
  }

  isLoadingQuestions = false;
  isLoadinSurveyDetails = true;
  tableData: any;
  tableColumns: any;
  tableColumnHeaders: any;
  apiData: any;
  videoUrl: string = "";
  videoUrlSet: boolean =  false;
  failureMessage = '';
  errorMessage = '';
  isSurveyCompleted: boolean = false;
  isSurveyDisabled: boolean = false;
  isSurveyStopped: boolean = false;
  isSurveyInterrupted: boolean = false;
  isSurveySaved: boolean = false;
  isPaginationRequired: boolean = false;
  skip = 0;
  limit = 10;
  currentPage = 1;
  numberOfRecords = 0;
  ngOnInit(): void {

    this.tableData = this.data.data;
    this.tableColumns = this.data.column;
    this.tableColumnHeaders = this.data.column;
    this.getSurveyDetails();
    this.getSurveyProceeding();
  }

  async getSurveyDetails() {
    this.isLoadinSurveyDetails = true;
    this.apiData = await this.dataService.getSurveyDetails(this.data.surveyId);
    if (this.apiData.statusCode !== 200) {
      this.isLoadinSurveyDetails = false;
      this.failureMessage = this.apiData && this.apiData.message ? this.apiData.message : 'Something went Wrong!';
      return;
    }
    let { survey } = this.apiData;
    this.videoUrl = survey.survey_video;
    this.videoUrlSet = true;
    if (survey) {
      this.isSurveySaved = survey.saved || false;
      this.isSurveyCompleted = survey.is_completed || false;
      this.isSurveyDisabled = survey.is_locked || false;
      this.isSurveyStopped = survey.is_stopped || false;
      this.isSurveyInterrupted = survey.is_interrupted || false;
      this.errorMessage = survey.errors && survey.errors.length > 0 ? survey.errors.join(" ") : '';
    }
  }

  async getSurveyProceeding() {

    this.isLoadingQuestions = true;
    this.apiData = await this.dataService.getSurveyProceeding(this.data.surveyId, this.skip, this.limit);
    this.numberOfRecords = this.apiData.proceedingsCount;
    if (this.apiData.statusCode !== 200) {
      this.isLoadingQuestions = false;
      this.failureMessage = this.apiData && this.apiData.message ? this.apiData.message : 'Something went Wrong!';
      return;
    }
    let { proceedings } = this.apiData;

    let formatedData = proceedings.map(data => {
      let question = {
        "Answers": data && data.answers && this.getAnswersFromObject(data.answer) || '',
        "Number": data && data.question && data.question.number || '',
        "Questions": data && data.question && data.question.question || '',
        "Selected Option": data && data.option && data && data.option.option,
      }
      return question;
    })
    this.tableData = formatedData;
    this.isLoadingQuestions = false;

  }


  getAnswersFromObject(answers) {
    if(answers) {
      return answers.join(' ');
    }
    return "";
  }

  closeModal() {
    this.dialog.closeAll();
  }

  openNewWindow() {
    const strWindowFeatures = "menubar=yes,location=yes,resizable=yes,scrollbars=yes,status=yes";
    let url = URL.createObjectURL(this.videoUrl);
   }
  onPageChange(page) {
    this.currentPage = page;
    this.skip = (page - 1) * this.limit;
    this.getSurveyProceeding();
  }
}
