import { MatSnackBar } from '@angular/material/snack-bar';
import { DataService } from './../../services/data.service';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Component, OnInit, Optional, Inject } from '@angular/core';


@Component({
  selector: 'app-answerfield-dialog',
  templateUrl: './answerfield-dialog.component.html',
  styleUrls: ['./answerfield-dialog.component.scss']
})
export class AnswerfieldDialogComponent implements OnInit {
  label = '';
  field = '';
  constructor(@Optional() @Inject(MAT_DIALOG_DATA) public data: any, private dialog: MatDialog, private dataService: DataService, private _snackbar: MatSnackBar) { }

  ngOnInit(): void {
    if (this.data && this.data.value) {
      this.label = this.data.value.label;
      this.field = this.data.value.field;
    }
  }

  onCancelClick() {
    this.dialog.closeAll();
  }
  onLabelChange(event) {
    this.label = event;
  }
  onFieldChange(event) {
    this.field = event;
  }
  async onSave() {
    if (this.label && this.field) {
      if (this.data.type === 'add') {
        const res = await this.dataService.createAnswerFields({ label: this.label, field: this.field })
        if (res.statusCode && res.statusCode === 201) {
          this._snackbar.open('Answerfield Created successfully', '', {
            duration: 2000,
            panelClass: ['greenSnackBar']
          })
          this.onCancelClick();
        } else {
          const msg = res.message ? res.message : 'Something went wrong';
          this._snackbar.open(msg, '', {
            duration: 2000,
            panelClass: ['redSnackBar']
          })
        }
      } else if (this.data.type === 'edit') {
        const res = await this.dataService.updateAnswerFields(this.data && this.data.value && this.data.value.id ? this.data.value.id : null, { label: this.label, field: this.field });
        if (res.message) {
          this._snackbar.open(res.message, '', {
            duration: 2000,
          })
        }
        this.onCancelClick();
      }
    }
  }
}
