import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AddInputComponent } from 'src/app/common/add-input/add-input.component';
import { TenantAuthModalComponent } from 'src/app/common/tenant-auth-modal/tenant-auth-modal.component';
import { UsersdialogComponent } from 'src/app/common/usersdialog/usersdialog.component';
import { Constants } from 'src/app/constants';
import { DataService } from '../../services/data.service';

@Component({
  selector: 'app-manage-tenents',
  templateUrl: './manage-tenents.component.html',
  styleUrls: ['./manage-tenents.component.scss']
})
export class ManageTenentsComponent implements OnInit {
  tableData: any;
  tableColumns: any;
  tableColumnHeaders: any;
  menu: string = 'inputs';
  tenents: Array<String> = [];
  isLoading = false;
  apiData: any;
  failureMessage = '';
  skip = 0;
  limit = 10;
  page = 1;
  orgName: string = "";
  logo: string = "";
  isRowClicked : boolean = false;
  numberOfRecords = 0;
  userName = localStorage.getItem('superAdminFirstName');
  profileImage = localStorage.getItem('superAdminAvatar');
  constructor(private dataService: DataService, private dialog: MatDialog, private modalDialog: MatDialog, private _snackbar: MatSnackBar) { }
  async ngOnInit(): Promise<void> {
    const loggedinAs = localStorage.getItem('logginedAs');
    if (loggedinAs === 'superadmin' && this.dataService.getCookie('superAdminToken')) {
      this.orgName = "Call Pilot";
      this.logo = "call-pilot-logo";
    }
    this.getTenents();
  }


  async getTenents() {
    this.isLoading = true;
    this.apiData = await this.dataService.getTenents(this.skip, this.limit);
    if (this.apiData.statusCode !== 200) {
      this.isLoading = false;
      this.failureMessage = this.apiData && this.apiData.message ? this.apiData.message : 'Something went Wrong!';
      return;
    }
    let { organizations } = this.apiData;
    organizations.map((data) => {
      data.user = {
        'profile': data.logo || '',
        'name': data.name || '',
      };
      return data;
    });
    this.numberOfRecords = this.apiData.organizationsCount;
    this.tableData = organizations;
    this.tableColumns = Constants.MANAGE_TENENTS_FIELDS;
    this.tableColumnHeaders = Constants.MANAGE_TENENTS_HEADER;
    this.isLoading = false;
  }

  async onToggleClick(event) {
    this.isLoading = true;
    let editItem = event && event[4] || "";
    let inputObj = {
      "input_type_name": editItem && editItem.input_type_name || "",
      "required": editItem && editItem.required || "",
      "disabled": editItem && editItem.disabled || "",
    }

    const res = await this.dataService.updateInputType(inputObj, editItem.id);
    if (res.message) {
      this._snackbar.open(res.message, '', {
        duration: 2000,
      })
    }
    this.isLoading = false;
  }


  onCellClick(event) {
    let editItem = event[2];
    if (event[0] === 'edit') {
      this.onEditClick(editItem);
    }
    else {
      let selectedItem = event[2];
      this.dialog.open(TenantAuthModalComponent, {
        width: "640px",
        data: {
          type: 'edit',
          selectedItem,
        }
      }).afterClosed().subscribe((result) => {
      });

    }
  }

  onRowClick(event) {
  }
  onEditClick(apiData) {
    let editItem = apiData;

    let dialogBoxSettings = {
      width: '500px',
      "max-height": 'fit-content',
      margin: '0 auto',
      disableClose: true,
      hasBackdrop: true,
      data: {
        "editables": editItem,
        "mode": "edit",
        "modalname": "tenants",
        "page": "tenants",
      }
    }
    this.dialog.open(UsersdialogComponent, dialogBoxSettings).afterClosed().subscribe((result) => {
      if (!result || result && result.event !== 'close') {
        this.getTenents();
      }
    });
  }
  async onDeleteClick(apiData) {
    let deleteItem = apiData;
    if (confirm("Are you sure want to delete " + deleteItem.id)) {
      const res = await this.dataService.deleteInputService(deleteItem.id);
      if (res.message) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
        })
      }
      this.getTenents();
    }
  }

  addNewApi() {
    let dialogBoxSettings = {
      width: '500px',
      "max-height": 'fit-content',
      margin: '0 auto',
      disableClose: true,
      hasBackdrop: true,
      data: {
        "mode": "add",
        "modalname": "tenants"
      }
    }

    this.dialog.open(UsersdialogComponent, dialogBoxSettings).afterClosed().subscribe((result) => {
      if (!result || result && result.event !== 'close') {
        this.getTenents();
      }
    });
  }

  onPageChange(page) {
    this.page = page;
    this.skip = (page - 1) * this.limit;
    this.getTenents();
  }


}
