import { FilterTableService } from './../../services/filter-table.service';
import { AfterViewInit, Component, ViewChild, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Constants } from './../../constants';


/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss']
})
export class TableComponent implements AfterViewInit {

  @Input() tableData: any = []
  @Input() pageName: any = "";
  @Input() tableColumns: string[] = [];
  @Output() onCellClicked: any = new EventEmitter<any>();
  paginationInfo: any;
  noData: boolean = false;
  searchString: any = '';
  displayedColumns: string[] = [];
  dataSource: MatTableDataSource<{}>;

  size = 4;
  pageIndex = 0;

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;

  constructor(private filterService: FilterTableService, public dialog: MatDialog) {

    this.dataSource = new MatTableDataSource(this.tableData);
    this.displayedColumns = this.tableColumns;
    // This will receive filter queries from other components
    this.filterService.invokeEvent.subscribe(value => {
      if (value) {
        this.applyFilter(value);
      }
    })
  }
  onPageChange(event) {
  }

  // paginate(event: any) {
  //   this.pageIndex=event;
  //   this.dataSource = this.tableData.slice(event * this.size - this.size, event * this.size);
  // }


  ngOnChanges() {
    if (this.tableData && this.tableData.length == 0) {
      this.noData = true;
    }
    ELEMENT_DATA = this.tableData;
    this.dataSource = new MatTableDataSource<any>(ELEMENT_DATA);
    this.displayedColumns = this.tableColumns;
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = typeof (event) === 'object' ? (event.target as HTMLInputElement).value : event;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  toggleStatus(value, index, column) {
    // Call API to update data
    this.dataSource.data[index][column] = value;
  }

  getColumnName(column) {
    if (column === "#") {
      return "#";
    }
    return column;
  }
  onCellClick(event, i, url, col) {
    const data = [event, i, url, col];
    this.onCellClicked.emit(data);
  }

}

let ELEMENT_DATA: any[] = [];


