import { SurveytypeDialogComponent } from './../../common/surveytype-dialog/surveytype-dialog.component';
import { SurveyHistoryDialogComponent } from './../survey-history-dialog/survey-history-dialog.component';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { RolesandpermissionsComponent } from 'src/app/common/rolesandpermissions/rolesandpermissions.component';
import { Constants } from 'src/app/constants';
import { UsersdialogComponent } from 'src/app/common/usersdialog/usersdialog.component';

import { intersection } from 'lodash';
import { formatDate } from '@angular/common';
import { DataService } from 'src/app/services/data.service';

import { NgxPermissionsService } from 'ngx-permissions';
import { ActivatedRoute, Router } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.scss']
})
export class HomePageComponent implements OnInit {
  surveyhistoryAlreadyLoaded: any = false;
  p: number = 2;
  userName: string | null = '';
  profileImage: string | null = '';
  // loading:boolean=true //show loader until data load from api
  selectedOption = 0;
  index = 0;
  initialTabIndex = 0;
  inputTypes: Array<String> = [];
  orgName: string = "";
  logo: string = "";
  permissions: string | null = "";
  permissionsArray: Array<String> = [];
  showSuperAdmin: boolean = false;
  isTokenExist = false;

  constructor(private router: Router, private activatedRoute: ActivatedRoute, private permissionsService: NgxPermissionsService, private dataService: DataService, private modalDialog: MatDialog, private _snackbar: MatSnackBar) { }


  async ngOnInit(): Promise<void> {
    const perm = Constants.PERMISSIONS;
    this.permissionsService.loadPermissions(perm);
    this.userName = localStorage.getItem('firstName');  
    this.permissions = localStorage.getItem('permissions');
    let otherPermissions = this.permissions ? this.permissions : "";
    if(otherPermissions) {
      this.permissionsService.loadPermissions(
        JSON.parse(otherPermissions)
        .filter(_ => _.allow).map(_ => _.resource_access)
      );
    }
    if(this.checkAndSetInitialIndex()) {
      this.profileImage = localStorage.getItem('avatar') !== null ? localStorage.getItem('avatar') : '../../../assets/avatar.png';
    
      const loggedinAs = localStorage.getItem('logginedAs');
  
      if (this.dataService.getCookie('superAdminToken')) {
        this.showSuperAdmin = true;
      }
  
      if (loggedinAs === 'superadmin' && this.dataService.getCookie('superAdminToken')) {
        this.orgName = "Call Pilot";
        this.logo = "call-pilot-logo";
      }
      else if(loggedinAs === 'admin' && this.dataService.getCookie('token')) {
        let organizationDetails  = await this.dataService.getOrganizationDetails();
        let {organization: {logo,name}} = organizationDetails
        localStorage.setItem('logo', logo);
        localStorage.setItem('organizationName', name);
        this.orgName = name;
        this.logo = logo;
      }
      return ;
    }

  }

  navigateToIndex(index){
    this.router.navigate([], {
     queryParams: {
       tab: index
     },
     queryParamsHandling: 'merge',
   });
  }

  async checkTokenAndNavigate() {
    return new Promise((response,reject)=>{
      if(this.dataService.getCookie('token')) {
        // If token exist proceed further
        response(null);
      }
      else {
        this.logout();
        reject();
      }
    })
    
  }

  deleteQueryParameterFromCurrentRoute()
  {
    var snapshot = this.activatedRoute.snapshot;
    const params = { ...snapshot.queryParams };
    delete params.tab
    this.router.navigate([], { queryParams: params });
  }

  clearTabParam() {
    this.router.navigate(
      ['.'], 
      { relativeTo: this.activatedRoute }
    );
  }

  
  async checkAndSetInitialIndex() {
    await this.checkTokenAndNavigate().then(()=>{
      this.isTokenExist = true;
      return true;
    })
    .catch(()=>{
      localStorage.clear();
      this.dataService.deleteAllCookies();
      this.clearTabParam();
      this.router.navigate(["/"]);
      this.isTokenExist = false;
      return false;
    })
    
    this.activatedRoute.queryParams.subscribe(params => {
      let tab = params['tab'];
      if(tab){
        this.index = tab;
        this.navigateToIndex(tab);
      }
      else {
        // inital route - survey tab
        let initialTab = 0;
        if(this.getPermission('manage_questions')) {
          initialTab = 0;
        }
        else if(!this.getPermission('manage_questions') && this.getPermission('manage_survey_history')) {
          initialTab = 1;
        }
        else if(!this.getPermission('manage_questions') && !this.getPermission('manage_survey_history') && this.isSettingsTabRequired()){
          initialTab = 2;
        }
        this.index = initialTab;
        this.navigateToIndex(initialTab);
      }
  });
  }
  

  optionClicked(option: number) {
    this.selectedOption = option;
  }
  onIndexChange(ind: number) {
    this.index = ind;
    if(this.index === 1) {
      this.surveyhistoryAlreadyLoaded = true;
    }
  }
  
  getPermission(permission, option=0) {
    let permissionList = this.permissions ? this.permissions : "";
    if(permissionList) {
      this.permissionsArray = JSON.parse(permissionList)
      .filter(_ => _.allow).map(_ => _.resource_access)
    }
    let final = this.permissionsArray.includes(permission);
    return final;
  }

  isSettingsTabRequired() {
    this.permissionsArray = JSON.parse(this.permissions ? this.permissions : "")
    .filter(_ => _.allow).map(_ => _.resource_access);
    return intersection(Constants.SETTINGS_PERMISSIONS, this.permissionsArray).length > 0;
  }

  getOption() {
    this.permissionsArray = JSON.parse(this.permissions ? this.permissions : "")
    .filter(_ => _.allow).map(_ => _.resource_access);
    let filteredPermission = Constants.SETTINGS_PERMISSIONS.filter(item => this.permissionsArray.includes(String(item)))
    if(filteredPermission){
      switch(filteredPermission[0]){
        case 'manage_api_services':
          return 1;
        case 'manage_survey_types':
          return 3;
        case 'manage_users':
          return 4;
        case 'manage_customer_api':
          return 5;
        case 'manage_customer_api':
          return 6;
        default:
          return 1;         
      }
    }
    else {
      return 0;
    } 
  }

  createSurvey() {
    this.modalDialog.open(SurveytypeDialogComponent, { width: '640px', data: { type: 'add' } }).afterClosed().subscribe(() => this.refresh());
  }
  refresh() {
    window.location.reload();
  }

  setIndex(index) {
    this.index = index;
    this.navigateToIndex(index);
  }

  async switchToSuperAdmin() {
    localStorage.setItem('logginedAs', 'superadmin');
    this.router.navigate(['super-admin/home']);
  }

  logout() {
    localStorage.clear();
    this.dataService.deleteAllCookies();
    this.clearTabParam();
    this.router.navigate(["/"]);
    this._snackbar.open('Logged out successfully', '', {
      duration: 2000,
      panelClass: ['greenSnackBar'],
    })
  }
}
