import { MatSnackBar } from '@angular/material/snack-bar';
import { AddApiComponent } from './../add-api/add-api.component';
import { TableComponent } from './../../common/table/table.component';
import { Constants } from './../../constants';
import { DataService } from './../../services/data.service';
import { Component, OnInit } from '@angular/core';
import { formatDate } from '@angular/common';
import { MatDialog } from '@angular/material/dialog';
@Component({
  selector: 'app-manage-api',
  templateUrl: './manage-api.component.html',
  styleUrls: ['./manage-api.component.scss']
})
export class ManageApiComponent implements OnInit {

  constructor(private dataService: DataService, private dialog: MatDialog, private _snackbar: MatSnackBar) { }
  page: number = 1;
  tableColumns = Constants.TABLE_COLUMNS.API_SERVICES;
  tableColumnHeaders = Constants.TABLE_COLUMNS.API_SERVICES_HEADER;
  tableData: any[] = [];
  apiData: any;
  isLoading = false;
  failureMessage = '';
  skip = 0;
  limit = 10;
  totalRecords = 0;
  ngOnInit(): void {
    this.getApi();
  }
  async getApi() {
    this.isLoading = true;
    try {
      this.apiData = await this.dataService.getApiServices(this.skip, this.limit);
      if (this.apiData.statusCode !== 200) {
        this.isLoading = false;
        this.failureMessage = this.apiData && this.apiData.message ? this.apiData.message : 'Something went Wrong!';
        return;
      }
      const tableArray: any = [];
      this.totalRecords = this.apiData.apiServicesCount;
      this.apiData.apiServices.forEach((element, i) => {
        let tabledata: any = {};
        tabledata['method'] = element.method;
        tabledata['url'] = element.url;
        tabledata['created_at'] = formatDate(element.created_at, 'dd MMM YYYY hh:mm a', 'en-US');
        tabledata['updated_at'] = formatDate(element.updated_at ? element.updated_at : element.created_at, 'dd MMM YYYY hh:mm a', 'en-US')
        tableArray.push(tabledata);
      });
      this.tableData = tableArray;
    } catch (err) {
      console.error('Error-----> ', err);
    }
    this.isLoading = false;
  }
  onCellClick(event) {
    // event -> table header, index, table data
    const currentData = this.apiData.apiServices[event[1]];
    if (event[0] === 'edit') {
      this.onEditClick(currentData);
    } else if (event[0] === 'delete') {
      this.onDeleteClick(currentData);
    }
  }
  onEditClick(apiData) {
    this.dialog.open(AddApiComponent, {
      width: "670px",
      data: {
        type: 'edit',
        apiData,
      }
    }).afterClosed().subscribe(() => this.getApi());
  }
  async onDeleteClick(apiData) {
    if (confirm("Are you sure want to delete " + apiData.url)) {
      const res = await this.dataService.deleteApiService(apiData.id);
      if (res.message) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
        })
      }
      this.getApi();
    }
  }
  addNewApi() {
    this.dialog.open(AddApiComponent, {
      width: "670px",
      data: {
        type: 'add',
        apiData: null,
      }
    }).afterClosed().subscribe(() => this.getApi());
  }
  onPageChange(page) {
    this.page = page;
    this.skip = (page - 1) * this.limit;
    this.getApi();
  }
}
