import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss']
})
export class ForgotPasswordComponent implements OnInit {

  constructor(private dataServices: DataService, private _snackBar: MatSnackBar, private router: Router) { }
  email = '';
  isButtonDisabled = false;
  isEmailSent = false;
  ngOnInit(): void {
  }
  getEmail(email: string) {
    this.email = email;
  }
  async onResetPassword() {
    this.isButtonDisabled = true;
    if (/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(this.email)) {
      try {
        var isSuperAdmin = this.router.url.includes('super-admin');
        const res = !isSuperAdmin ? await this.dataServices.forgotPassword(this.email) : await this.dataServices.superAdminForgotPassword(this.email);
        if (res.statusCode === 200) {
          if (res.message) {
            this._snackBar.open(res.message, '', {
              duration: 2000,
              panelClass: ['greenSnackBar'],
            });
            this.isEmailSent = true;
          }
        }
        else {
          let errorMsg;
          if (res.error && res.error.message) {
            errorMsg = res.error.message;
          } else {
            errorMsg = 'Something went wrong';
          }
          this._snackBar.open(errorMsg, '', {
            duration: 2000,
            panelClass: ['redSnackBar'],
          });
        }
      } catch (err) {
        this._snackBar.open(err, '', {
          duration: 2000,
          panelClass: ['redSnackBar'],
        });
        console.error("Pasword reset error: ", err);
      }
    } else {
      this._snackBar.open('Invalid Email', '', {
        duration: 2000,
        panelClass: ['redSnackBar'],
      });
    }
    this.isButtonDisabled = false;
  }
  backToLogin() {
    var isSuperAmdin = this.router.url.includes('super-admin');
    if(isSuperAmdin)
    {
     this.router.navigate(['/super-admin'])
    }else 
    {
    this.router.navigate(['/']);
    }
  }
}
