import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/services/data.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Constants } from '../../constants';

@Component({
  selector: 'app-manage-app-releases',
  templateUrl: './manage-app-releases.component.html',
  styleUrls: ['./manage-app-releases.component.scss']
})
export class ManageAppReleasesComponent implements OnInit {
  
  androidVersion: string = "";
  iosVersion: string = "";
  newAndroidVersion: string = "";
  newIosVersion: string = "";
  versionRegex: string = "^\d+(\.\d+){0,2}$";
  buttonDisabled: boolean = true;
  androidId: number = 0;
  iosId: number = 0;


  isLoading = false;
  apiData: any;
  

  constructor(private dataService: DataService,private _snackbar: MatSnackBar) { }
  async ngOnInit(): Promise<void> {
    ;
    this.getAndroidReleaseVersion();
    this.getIOSReleaseVersion();
  }
  

  async getAndroidReleaseVersion() {
    this.isLoading = true;
    this.apiData = await this.dataService.getAndroidReleases();
    if (this.apiData.statusCode && this.apiData.statusCode === 200) {
      let { appRelease } = this.apiData;
      if(appRelease) {
        this.androidVersion = appRelease.version;
        this.newAndroidVersion = appRelease.version;
        this.androidId = appRelease.id;
      }
      this.isLoading = false;
    }
    else {
      const message = this.apiData.message ? this.apiData.message : 'Something went wrong while Android release version';
      this._snackbar.open(message, '', {
        duration: 2000,
      })
      this.isLoading = false;
    }
  }

  async getIOSReleaseVersion() {
    this.isLoading = true;
    this.apiData = await this.dataService.getIOSReleases();
    if (this.apiData.statusCode && this.apiData.statusCode === 200) {
      let { appRelease } = this.apiData;
      if(appRelease) {
        this.iosVersion = appRelease.version;
        this.newIosVersion = appRelease.version;
        this.iosId = appRelease.id;
      }
      this.isLoading = false;
    }
    else {
      const message = this.apiData.message ? this.apiData.message : 'Something went wrong while IOS release version';
      this._snackbar.open(message, '', {
        duration: 2000,
      })
      this.isLoading = false;
    }
  }
 
  onAndroidVersionChange(value: string) {
    this.newAndroidVersion = value;
    this.validate();
  }

  onIOSVersionChange(value: string) {
    this.newIosVersion = value;
    this.validate();
  }

  async onSaveClick() {
    this.isLoading = true;
    let isUpdated = false;

    try {
      if(this.androidVersion != this.newAndroidVersion) {
        const androidObj = {
          "version": this.newAndroidVersion,
          "release_date": new Date().toISOString()
          ,
        }
        let res = await this.dataService.updateAndroidReleases(this.androidId, androidObj);
        if(res.statusCode && res.statusCode === 200){
          isUpdated = true;
          this.androidVersion = this.newAndroidVersion;
        }
        else {
          this._snackbar.open("something went wrong", '', {
            duration: 2000,
            panelClass: ['redSnackBar'],
          });
        }
      }

      if(this.iosVersion != this.newIosVersion) {
        const iosObj = {
          "version": this.newIosVersion,
          "release_date": new Date().toISOString()
          ,
        }
        let res = await this.dataService.updateIOSReleases(this.iosId, iosObj);
        if(res.statusCode && res.statusCode === 200){
          isUpdated = true;
          this.iosVersion = this.newIosVersion;
        }
        else {
          this._snackbar.open("something went wrong", '', {
            duration: 2000,
            panelClass: ['redSnackBar'],
          });
        }
      }

    if(isUpdated) {
      this.validate();
      this._snackbar.open('Successfully updated', '', {
        duration: 2000,
        panelClass: ['greenSnackBar'],
      })
    }
    this.isLoading = false;
  }
  catch(err) {
    this._snackbar.open(err, '', {
      duration: 2000,
      panelClass: ['redSnackBar'],
    });
  }

  }

  validate() {
    if (Constants.RELEASE_VERSIONS.VERSION_REGEX.test(this.newAndroidVersion) && Constants.RELEASE_VERSIONS.VERSION_REGEX.test(this.newIosVersion) 
    && ( this.newAndroidVersion != this.androidVersion || this.newIosVersion != this.iosVersion )
    ) {
     this.buttonDisabled = false;
  } else {
    this.buttonDisabled = true;
  }    
  }

}
