
import { DataService } from './../../services/data.service';
import { FilterTableService } from './../../services/filter-table.service';
import { TableComponent } from './../../common/table/table.component';
import { HttpClient } from '@angular/common/http';
import { DownloadService } from './../../services/download.service';
import { SurveyHistoryDialogComponent } from './../survey-history-dialog/survey-history-dialog.component';
import { Constants } from './../../constants';
import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { saveAs } from 'file-saver';
// import { saveAs } from 'file-saver';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SortdialogComponent } from 'src/app/common/sortdialog/sortdialog.component';
import { HtmlTableComponent } from 'src/app/common/html-table/html-table.component';
import { ViewChild, ElementRef, Renderer2 } from '@angular/core';
import { formatDate } from '@angular/common';
import * as moment from 'moment';
import { NgxPermissionsService } from 'ngx-permissions';
import * as Highcharts from 'highcharts';
window.Highcharts = Highcharts;
@Component({
  selector: 'app-survey-history',
  templateUrl: './survey-history.component.html',
  styleUrls: ['./survey-history.component.scss']
})
export class SurveyHistoryComponent implements OnInit {
  @ViewChild(SortdialogComponent) filterDialog: SortdialogComponent;
  @ViewChild(HtmlTableComponent) tableComponent: HtmlTableComponent;
  @ViewChild('chartData') div: ElementRef;
  options: any = [];
  sortByOptions: any = [];
  defaultOption: number = 1;
  sortOptions: any = [
    { label: 'Sort by Latest', value: 'latest' },
    { label: 'Sort by Oldest', value: 'oldest' }
  ];
  sortByOption: any = Constants.TABLE_COLUMNS.SORT_BY;
  sortByColumns: any = Constants.TABLE_COLUMNS.SORT_COLUMNS;

  defaultSortOption: string = 'latest';
  tableData = [];
  tableColumns = Constants.TABLE_COLUMNS.SURVEY_HISTORY;
  tableColumnHeader = Constants.TABLE_COLUMNS.SURVEY_HISTORY_HEADER;
  sortTableColumnData = [];
  sortColumn = 'created_at' ;
  sortType = 'DESC';
  skip = 0;
  limit = 50;
  numberOfRecords = 0;
  currentPage = 1;
  search = '';
  completed = null;
  stopped = null;
  saved = null;
  locked = null;
  interrupted = null;
  uploaded = null;
  filterUploadedToCRM = null;
  dateCreated: any = [];
  dateModified: any = [];
  isLoading = true;
  failureMessage = '';
  isDeleteBatch: any = false;
  isUploadToCRM: boolean = false;
  apiData: any;
  surveyDetails: any = [];
  isRefreshDisabled = false;
  deleteSurveyIds : any = [];
  uploadToCRMIds : any = [];
  surveyPercentages: any = {};
  percentageArray:any = [];
  lastWeekChartData: any = [];
  chartElement :any = "";
  permissions: string | null = "";
  permissionsArray: Array<String> = [];
  constructor(private renderer: Renderer2, private permissionsService: NgxPermissionsService, private _snackbar: MatSnackBar, private dialog: MatDialog, private downloadService: DownloadService, private filterService: FilterTableService, private dataService: DataService) { 
  }

  chartOptions: any = {};
  lastWeekChartOptions: any = {}

  ngOnInit(): void {
    this.initialSetup();
  }

  async createChart() {
    await this.getLastWeekChartData();
    await this.setPercentages(this.surveyDetails);
    await this.setCharOption().then(()=>{
      this.chartOptions.chart.renderTo = 'chart';
      Highcharts.chart("chart", this.chartOptions );
    });
    await this.setLastWeekCharOption().then(()=>{
        this.chartOptions.chart.renderTo = 'oneWeekChart';
        Highcharts.chart("oneWeekChart", this.lastWeekChartOptions );
    })
  }

  getPermission(permission, option=0) {
    let permissionList = this.permissions ? this.permissions : "";
    if(permissionList) {
      this.permissionsArray = JSON.parse(permissionList)
      .filter(_ => _.allow).map(_ => _.resource_access)
    }
    let final = this.permissionsArray.includes(permission);
    return final;
  }

  bytesToSize(bytes, decimals = 2) {
    if (bytes == 0) return '0 Bytes';

    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

    const i = Math.floor(Math.log(bytes) / Math.log(k));

    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
 }

  async initialSetup(){
    const perm = Constants.PERMISSIONS;
    this.permissionsService.loadPermissions(perm);
    this.permissions = localStorage.getItem('permissions');
    let otherPermissions = this.permissions ? this.permissions : "";
    if(otherPermissions) {
      this.permissionsService.loadPermissions(
        JSON.parse(otherPermissions)
        .filter(_ => _.allow).map(_ => _.resource_access)
      );
    }

    if(!this.getPermission('delete_surveys')) {
      let headers = Constants.TABLE_COLUMNS.SURVEY_HISTORY_HEADER
      let filteredHeader =  headers.filter(e => e !== 'delete')
      this.tableColumnHeader = filteredHeader;
    }

    await this.getLastWeekChartData();
    await this.setSurveysTypes();
    await this.getSurveys();
  }

  async setPercentages(surveyValues) {
    let {android, ios, failed, completed, total, stopped, uploaded, interrupted, saved, upload_to_crm} = surveyValues;

    return new Promise((resolve,reject) => {
      this.surveyPercentages['total']= Number(total);
      this.surveyPercentages['android']= Number(android);
      this.surveyPercentages['ios']= Number(ios);
      this.surveyPercentages['completed']= Number(completed);
      this.surveyPercentages['failed']= Number(failed);
      this.surveyPercentages['stopped']= Number(stopped);
      this.surveyPercentages['uploaded']= Number(uploaded);
      this.surveyPercentages['interrupted']= Number(interrupted);
      this.surveyPercentages['saved']= Number(saved);
      this.surveyPercentages['upload_to_crm']= Number(upload_to_crm);  

      this.percentageArray =[
      ['Total', this.surveyPercentages['total']], 
       ['Uploaded', this.surveyPercentages['uploaded']],
       ['Android', this.surveyPercentages['android']],
       ['Ios', this.surveyPercentages['ios']],
       ['Completed', this.surveyPercentages['completed']],
       ['Failed', this.surveyPercentages['failed']],
       ['Stopped', this.surveyPercentages['stopped']],
       ['Interrupted', this.surveyPercentages['interrupted']],
       ['Saved', this.surveyPercentages['saved']],
       ['Uploaded To CRM', this.surveyPercentages['upload_to_crm']],
      ];

      resolve(0);
    });
  }

  setCharOption() {
    return new Promise((resolve,reject) => {
      
      let  chartOptions = {
          chart: {
              marginLeft: 100,
              //  plotAreaWidth: 50,
                // plotAreaHeight: 450,
          },
      
          title: {
              text: 'Survey History - Stat'
          },
      
          yAxis: {
              title: {
                  text: ''
              }
          },
      
          xAxis: {
              type: 'category',
              min: 0,
              labels: {
                  // animate: false
              }
          },
      
          legend: {
              enabled: false
          },
          colors: [
            '#008000',
            '#EE82EE',
            '#FF69B4',
            '#FFD700',
            '#FFA07A',
            '#6495ED',
            '#CCCCFF',
            '#FF0000',
        ],
          series: [{
              type: 'bar',
              colorByPoint: true,
              zoneAxis: 'x',
              dataLabels: {
                  enabled: true,
                  format: '{y:}'
              },
              dataSorting: {
                  enabled: true,
                  sortKey: 'y'
              },
              data: this.percentageArray
          }]
      
      };
    this.chartOptions = chartOptions;
    resolve(0);
    });
  }

  async setLastWeekCharOption() {
    return new Promise((resolve,reject) => {
      
      let  chartOptions = {
          chart: {
              type: 'spline'
          },
          title: {
              text: 'Survey History'
          },
          subtitle: {
              text: 'Last week stat'
          },
          xAxis: {
              type: 'datetime',
              dateTimeLabelFormats: { // don't display the dummy year
                  month: '%e. %b',
                  year: '%b'
              },
              title: {
                  text: 'Date'
              }
          },
          yAxis: {
              title: {
                  text: 'Survey Count'
              },
              min: 0
          },
          tooltip: {
              headerFormat: '<b>{series.name}</b><br>',
              pointFormat: '{point.x:%e. %b}: {point.y:.0f}'
          },
      
          plotOptions: {
              series: {
                  marker: {
                      enabled: true
                  }
              }
          },
      
          colors: ['#6CF', '#FFA07A', '#39F', '#008000'],
          series: this.lastWeekChartData,
      
          responsive: {
              rules: [{
                  condition: {
                      maxWidth: 500
                  },
                  chartOptions: {
                      plotOptions: {
                          series: {
                              marker: {
                                  radius: 2.5
                              }
                          }
                      }
                  }
              }]
          }

    }
    this.lastWeekChartOptions = chartOptions;

    resolve(0);
    });

 
  }

  async setSurveysTypes() {
    const res = await this.dataService.getAllSurveys()
    const surveyArray: any = [];
    if (res.statusCode && res.statusCode === 200) {
      res.surveyTypes.forEach(element => {
        let surveyData = {};
        surveyData['label'] = element.name;
        surveyData['value'] = element.id;
        surveyArray.push(surveyData);
      });
      this.options = surveyArray;
    }
  }
async getSurveys() {
  this.isLoading = true;
  this.isRefreshDisabled = true;
  this.apiData = await this.dataService.getSurveyHistory(this.defaultOption, this.limit, this.skip, this.search, this.completed, this.stopped, this.locked, this.interrupted, this.saved ,this.uploaded, this.filterUploadedToCRM, this.dateCreated, this.dateModified, this.sortColumn, this.sortType);
  // Get Location from geocoading using lat and lang
  // let {surveys} = this.apiData;
  // let updatedSurveysData = await Promise.all(
  //   surveys.map(async survey => {
  //     let locationResponse = survey.latitude && survey.longitude ? await this.dataService.getAddressFromLatAndLang(survey.latitude, survey.longitude) : "";
  //     survey.address = locationResponse ? locationResponse.results[2]['formatted_address'] : "";
  //     return survey;
  //   })
  // )
  const survey: any = [];
  if (this.apiData.statusCode && this.apiData.statusCode === 200) {
    this.numberOfRecords = this.apiData.surveysCount
    this.apiData.surveys.forEach(async element => {
        let currentSurvey = {};
        currentSurvey['errors'] = element.errors.join(" ");
        currentSurvey['id'] = element.id;
        currentSurvey['user_id'] = element.user_id;
        currentSurvey['completed'] = element.is_completed;
        currentSurvey['platform'] = element.platform ? element.platform  : "";
        currentSurvey['device_name'] = element.device_name ? element.device_name  : "";
        currentSurvey['version'] = element.version ? element.version  : "";
        currentSurvey['isStopped'] = element.is_stopped;
        currentSurvey['isLocked'] = element.is_locked;
        currentSurvey['isInterrupted'] = element.is_interrupted;
        currentSurvey['username'] = element.username ? element.username : "";
        currentSurvey['created_at'] = formatDate(element.created_at, 'dd MMM YYYY hh:mm a', 'en-US');
        currentSurvey['updated_at'] = formatDate(element.updated_at, 'dd MMM YYYY hh:mm a', 'en-US');
        currentSurvey['video'] = element.survey_video;
        currentSurvey['uploaded_from'] = element.uploaded_from ? element.uploaded_from : "";
        currentSurvey['app_version'] = element.app_version ? element.app_version : "";
        currentSurvey['battery_percentage'] = element.battery_percentage ? element.battery_percentage : "";
        currentSurvey['location'] = element.address ? element.address : "";;
        currentSurvey['internet_speed'] = element.internet_speed ? element.internet_speed : "";
        currentSurvey['saved'] = element.saved;
        currentSurvey['uploaded_to_crm'] = element.upload_to_crm ? element.upload_to_crm : false;
        currentSurvey['survey_video_size'] = element.survey_video_size ? this.bytesToSize(element.survey_video_size) : "";
        currentSurvey['checkbox'] = false;
        survey.push(currentSurvey);
      });
      this.tableData = survey;
      let {stats} = this.apiData;

      Object.keys(stats).forEach(e => {
        stats[e] = Number(stats[e]);
      });
      this.surveyDetails = stats;
      // this.createChart();
    } else {
      if (this.apiData.status !== 200) {
        this.failureMessage = this.apiData.message ? this.apiData.message : 'Something went wrong!';
      }
    }
    this.isRefreshDisabled = false;
    this.isLoading = false;
  }

  refreshSurveys() {
    this.skip = 0;
    this.limit = 50;
    this.currentPage = 1;
    this.deleteSurveyIds = [];
    this.getSurveys();
  }


  isValueReset() {
    if(this.sortColumn == 'created_at' && this.sortType == 'DESC')  {
      return true;
    }
    else {
      return false;
    }
  }

  resetSort() {
    this.sortColumn = 'created_at' ;
    this.sortType = 'DESC';

    this.skip = 0;
    this.limit = 50;
    this.currentPage = 1;
    this.deleteSurveyIds = [];
    this.getSurveys();
  }

  onSortColumnChange(id) {
    this.sortColumn = id.value;
    this.skip = 0;
    this.limit = 50;
    this.currentPage = 1;
    this.deleteSurveyIds = [];
    this.getSurveys();
  }

  onSortColumnBy(id) {
    this.sortType = id.value;
    this.skip = 0;
    this.limit = 50;
    this.currentPage = 1;
    this.deleteSurveyIds = [];
    this.getSurveys();
  }

  onSurveyTypeChange(id) {
    this.defaultOption = id.value;
    this.skip = 0;
    this.limit = 50;
    this.currentPage = 1;
    // To reset the delete batch
    this.deleteSurveyIds = [];
    this.isDeleteBatch = false;
    this.resetDeleteIdData();
    this.resetUploadIdData();
    this.getSurveys();
  }

  onSortClicked(item) {
    let index = item['index'];
    let updatedObj = item['updatedObj'];
    
    let data = updatedObj[index];

    this.sortTableColumnData = updatedObj;

    if(data.sortType == 'none') {
      this.resetSort();
    }
    else {

      let isValidSort: Boolean = false;
      let tableValidColumns =
                  ['user_id',
                  'username',
                  'platform',
                  'device_name',
                  'version',
                  'is_completed',
                  'is_stopped',
                  'is_locked',
                  'is_interrupted',
                  'app_version',
                  'battery_percentage',
                  'internet_speed',
                  'uploaded_from',
                  'upload_to_crm',
                  'survey_video_size',
                  'latitude',
                  'longitude',
                  'survey_video',
                  'address',
                  'created_at',
                  'updated_at']

     
          if(data.columnName == 'completed') {
            this.sortColumn =  'is_completed';
            isValidSort = true;
          }
          else if(data.columnName == 'isStopped') {
            this.sortColumn =  'is_stopped'; 
            isValidSort = true;
          }
          else if(data.columnName == 'isLocked') {
            this.sortColumn =  'is_locked'; 
             isValidSort = true;
          }

          else if(data.columnName == 'isInterrupted') {
            this.sortColumn =  'is_interrupted'; 
            isValidSort = true;
          }

          else if(data.columnName == 'saved') {
            this.sortColumn =  'is_interrupted'; 
            isValidSort = true;
          }
          else if(data.columnName == 'uploaded_to_crm') {
            this.sortColumn =  'upload_to_crm'; 
            isValidSort = true;
          }

        
          else {
            if(tableValidColumns.indexOf(data.columnName) !== -1) {
              this.sortColumn =  data.columnName;
              isValidSort = true;
            }
            else {
              isValidSort = false;
            }
          }

          if( isValidSort) {
            this.sortType = data.sortType;
            this.skip = 0;
            this.limit = 50;
            this.currentPage = 1;
            this.deleteSurveyIds = [];
            this.getSurveys();
          }          
  }
  }

  async onCellClick(data) {
    const currentData = data[2];
    if (data[0] === 'delete') {
      this.onDeleteClick(currentData);
    }
    else if (data[3] !== 'video') {
      this.dialog.open(SurveyHistoryDialogComponent, {
        width: '1328px',
        data: {
          surveyId: this.apiData.surveys[data[1][0]].id,
          column: Constants.TABLE_COLUMNS.MODAL_COLUMNS,
          data: Constants.TABLE_DATA.MODAL_DATA,
          video: data[2]['video'],
        }
      });
    } else if (data[3] === 'video') {
      if (data[2]) {
        window.open(data[2]);
      }
    }
  }

  async onCheckboxClick(data) {
    const deleteItem = data[1];
    const index = this.deleteSurveyIds.indexOf(deleteItem.id);
    if (index > -1) {
      this.deleteSurveyIds.splice(index, 1);
    }
    else {
      this.deleteSurveyIds.push(deleteItem.id); 
    }
  }

  async onUploadCheckboxClick(data) {
    const uploadItem = data[1];
    const index = this.uploadToCRMIds.indexOf(uploadItem.id);
    if (index > -1) {
      this.uploadToCRMIds.splice(index, 1);
    }
    else {
      this.uploadToCRMIds.push(uploadItem.id); 
    }
  }

  async onAllSelectClick(status) {
    this.deleteSurveyIds = [];
    if(status) {
      this.tableData.forEach(data => {
        this.deleteSurveyIds.push(data["id"]);
      })
    }
    else {
      this.deleteSurveyIds = [];
    }
  }

  async onAllSelectUploadClick(status) {
    this.uploadToCRMIds = [];
    if(status) {
      this.tableData.forEach(data => {
        if(!data['uploaded_to_crm']) {
          this.uploadToCRMIds.push(data["id"]);
        }
      })
    }
    else {
      this.uploadToCRMIds = [];
    }
  }

  async onDeleteClick(apiData) {
    let deleteItem = apiData;
    console.log("deleteItem", deleteItem);
    if (confirm("Are you sure want to delete?")) {
      const res = await this.dataService.deleteSurveyHistory(deleteItem.id);
      if (res.message) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
          panelClass: ['greenSnackBar']
        })
      }
      this.refreshSurveys();
    }
  }
  
  deleteBatch() {
    this.isDeleteBatch = !this.isDeleteBatch;
    this.deleteSurveyIds = [];
    this.isUploadToCRM = false;
    this.tableComponent.resetDeleteColumn();
  }

  uploadToCRMBatch() {
    this.isUploadToCRM = !this.isUploadToCRM;
    this.uploadToCRMIds = [];
    this.isDeleteBatch = false;
    this.tableComponent.resetUploadColumnData();
  }

  async deleteAllSurvey() {
    if (confirm("Are you sure want to delete the selected surveys?")) {
      const res = await this.dataService.deleteBatchSurveyHistory({
        surveys: this.deleteSurveyIds
      });
      if (res.statusCode == 200) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
          panelClass: ['greenSnackBar']
        })
        this.resetDeleteIdData();
        this.refreshSurveys();
      }
      else {
        if (res.error && res.message) {
          let errorMsg = res.message;
          this._snackbar.open(errorMsg, '', {
            duration: 2000,
            panelClass: ['redSnackBar']
          })
        }
      }
    }
  }

  async uploadAllSurveyToCRM() {
    if (confirm("Are you sure want to upload selected surveys to CRM?")) {
      const res = await this.dataService.uploadSurveysToCRM({
        surveys: this.uploadToCRMIds,
        "upload_to_crm": true,
      });
      if (res.statusCode == 200) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
          panelClass: ['greenSnackBar']
        })
        this.resetUploadIdData();
        this.refreshSurveys();
      }
      else {
        if (res.error && res.message) {
          let errorMsg = res.message;
          this._snackbar.open(errorMsg, '', {
            duration: 2000,
            panelClass: ['redSnackBar']
          })
        }
      }
    }
  }

  resetDeleteIdData() {
    this.deleteSurveyIds = [];
    this.isDeleteBatch = false;
    this.tableComponent.resetDeleteColumn();
  }

  resetUploadIdData() {
    this.uploadToCRMIds = [];
    this.isUploadToCRM = false;
    this.tableComponent.resetUploadColumnData();
  }

  searchById(id) {
    this.search = id;
    this.skip = 0;
    this.limit = 50;
    this.currentPage = 1;
    this.getSurveys();
  }
  applyFilter(event) {
    this.dateCreated = event.datecreated.startDate && event.datecreated.endDate ? [formatDate(event.datecreated.startDate, 'dd MMM YYYY hh:mm a', 'en-US'), formatDate(event.datecreated.endDate, 'dd MMM YYYY hh:mm a', 'en-US')] : [];
    this.dateModified = event.dateModified.startDate && event.dateModified.endDate ? [formatDate(event.dateModified.startDate, 'dd MMM YYYY hh:mm a', 'en-US'), formatDate(event.dateModified.endDate, 'dd MMM YYYY hh:mm a', 'en-US')] : [];
    this.dateCreated = this.dateCreated.map(_ => new Date(_).toUTCString());
    this.dateModified = this.dateModified.map(_ => new Date(_).toUTCString());
    this.completed = event.checkboxFilters['completed'];
    this.stopped = event.checkboxFilters['stopped'];
    this.saved = event.checkboxFilters['saved'];
    this.locked = event.checkboxFilters['locked'];
    this.interrupted = event.checkboxFilters['interrupted'];
    this.filterUploadedToCRM = event.checkboxFilters['uploadedToCRM'];
    this.uploaded = event.checkboxFilters['uploaded'];
    this.skip = 0;
    this.limit = 50;
    this.currentPage = 1;
    this.getSurveys();
  }
  onPageChange(page) {
    this.currentPage = page;
    this.skip = (page - 1) * this.limit;
    this.getSurveys();
  }

  async getLastWeekChartData() {
    let fromDate = moment().subtract(7, 'days').startOf('day').format("MM/DD/YYYY, hh:mm:ss a");
    let toDate = moment().endOf('day').format("MM/DD/YYYY, hh:mm:ss a");
    try {
      let chartStat = await this.dataService.getSurveyHistoryLastWeekChart(this.defaultOption,fromDate,toDate);
      let {stats:chartData} = chartStat;
      var total = {
        name: "Total",
        data: [] as any,
      };
      var completed = {
        name: "Completed",
        data: [] as any,
      };
      var uploaded = {
        name: "Uploaded",
        data: [] as any
      };
      
      chartData.forEach(data1 => {

        let completedArr:Array<any> = [];
        completedArr.push(new Date(data1["date"]).getTime());
        completedArr.push(Number(data1["completed"]));
        completed.data.push(completedArr);

        let uploadedArr:Array<any> = [];
        uploadedArr.push(new Date(data1["date"]).getTime());
        uploadedArr.push(Number(data1["uploaded"]));
        uploaded.data.push(uploadedArr);

        let sumArr:Array<any> = [];
        sumArr.push(new Date(data1["date"]).getTime());
        sumArr.push(Number(data1["sum"]));
        total.data.push(sumArr);
      });

      this.lastWeekChartData = [completed, uploaded, total];

    }
    catch(err){
      console.error(err)
    }
  }
}
