import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-pagination',
  templateUrl: './pagination.component.html',
  styleUrls: ['./pagination.component.scss']
})
export class PaginationComponent implements OnInit {
  @Input() numberOfRecords = 0;
  @Input() recordsPerPage = 10;
  @Output() onPageChange = new EventEmitter<any>();
  @Input() currentPage = 1;
  numberOfPages = 0;
  pageArray: any = [];
  shownPages: any = [];
  constructor() { }

  ngOnInit(): void {
    if (this.numberOfRecords) {
      this.numberOfPages = Math.ceil(this.numberOfRecords / this.recordsPerPage);
      for (let i = 1; i <= this.numberOfPages; i++) {
        this.pageArray.push(i);
      }
      this.getShownPages(this.currentPage);
    }
  }
  prevPage() {
    if (this.currentPage > 1) {
      this.currentPage--;
      this.onPageChange.emit(this.currentPage);
      this.getShownPages(this.currentPage);
    }
  }
  nextPage() {
    if (this.currentPage < this.numberOfPages) {
      this.currentPage++;
      this.onPageChange.emit(this.currentPage);
      this.getShownPages(this.currentPage);
    }
  }

  pageClicked(page) {
    this.currentPage = page;
    this.onPageChange.emit(this.currentPage);
    this.getShownPages(page);
  }

  getShownPages(currentPage) {
    // Shown pages will hold the range 2 pages before current page, current page and 2 page after current page
    //  Will be like   1 2 3 ... 7 8 [9] 10 11 ... 16 17

    // To handle case when current page > 3 and number of page > 5.
    if (this.numberOfPages > 5 && this.currentPage > 3) {
      this.shownPages = this.pageArray.slice(currentPage - 3, currentPage + 2);
    }

    // To handle case when total pages > 5 and current page is below 3
    else if (this.numberOfPages > 5 && this.currentPage <= 3) {
      this.shownPages = this.pageArray.slice(0, 5);
    }

    // To handle case when total page < 5
    else {
      this.shownPages = this.pageArray;
    }
  }
}
