import { MatSnackBar } from '@angular/material/snack-bar';
import { DataService } from './../../services/data.service';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Component, OnInit, Input } from '@angular/core';
import { Inject } from '@angular/core';
import { Optional } from '@angular/core';

@Component({
  selector: 'app-add-api',
  templateUrl: './add-api.component.html',
  styleUrls: ['./add-api.component.scss']
})
export class AddApiComponent implements OnInit {
  @Input() page: string = "manage-customer-api";
  @Input() isHeaderRequired: string = "true";

  requestOptions = [
    { label: 'GET', value: 'get' },
    { label: 'POST', value: 'post' },
    { label: 'PUT', value: 'put' },
    { label: 'DELETE', value: 'delete' },
    { label: 'PATCH', value: 'patch' },
  ];
  requestCurrentOption = 'get';
  url = '';
  reqHeader = [
    {
      key: '',
      value: '',
    },
  ];
  headerOptions = [
    { label: 'A-IM', value: 'A-IM' },
    { label: 'Accept', value: 'Accept' },
    { label: 'Accept-Charset', value: 'Accept-Charset' },
    { label: 'Accept-Datetime', value: 'Accept-Datetime' },
    { label: 'Accept-Encoding', value: 'Accept-Encoding' },
    { label: 'Accept-Language', value: 'Accept-Language' },
    { label: 'Access-Control-Request-Method', value: 'Access-Control-Request-Method' },
    { label: 'Access-Control-Request-Headers', value: 'Access-Control-Request-Headers' },
    { label: 'Authorization', value: 'Authorization' },
    { label: 'Cache-Control', value: 'Cache-Control' },
    { label: 'Connection', value: 'Connection' },
    { label: 'Content-Encoding', value: 'Content-Encoding' },
    { label: 'Content-Length', value: 'Content-Length' },
    { label: 'Content-MD5', value: 'Content-MD5' },
    { label: 'Content-Type', value: 'Content-Type' },
    { label: 'Cookie', value: 'Cookie' },
    { label: 'Date', value: 'Date' },
    { label: 'Expect', value: 'Expect' },
    { label: 'Forwarded', value: 'Forwarded' },
    { label: 'From', value: 'From' },
    { label: 'Host', value: 'Host' },
    { label: 'HTTP2-Settings', value: 'HTTP2-Settings' },
    { label: 'If-Match', value: 'If-Match' },
    { label: 'If-Modified-Since', value: 'If-Modified-Since' },
    { label: 'If-None-Match', value: 'If-None-Match' },
    { label: 'If-Range', value: 'If-Range' },
    { label: 'If-Unmodified-Since', value: 'If-Unmodified-Since' },
    { label: 'Max-Forwards', value: 'Max-Forwards' },
    { label: 'Origin', value: 'Origin' },
    { label: 'Pragma', value: 'Pragma' },
    { label: 'Prefer', value: 'Prefer' },
    { label: 'Proxy-Authorization', value: 'Proxy-Authorization' },
    { label: 'Range', value: 'Range' },
    { label: 'Referer', value: 'Referer' },
    { label: 'TE', value: 'TE' },
    { label: 'Trailer', value: 'Trailer' },
    { label: 'Transfer-Encoding', value: 'Transfer-Encoding' },
    { label: 'User-Agent', value: 'User-Agent' },
    { label: 'Upgrade', value: 'Upgrade' },
    { label: 'Via', value: 'Via' },
    { label: 'Warning', value: 'Warning' },
  ];
  isLoading = false;
  constructor(@Optional() @Inject(MAT_DIALOG_DATA) public data: any, private dialog: MatDialog, private dataServices: DataService, private _snackbar: MatSnackBar) { }

  ngOnInit(): void {
    if (this.data && this.data.type === 'edit') {
      this.isLoading = true;
      this.reqHeader = this.data.apiData.headers;
      this.requestCurrentOption = this.data.apiData.method;
      this.url = this.data.apiData.url;
      this.reqHeader.push({
        key: '',
        value: '',
      })
      this.isLoading = false;
    } else if (!this.data) {
      // If there is no data, then it is not a dialog. We are getting the dialog params and storing it in data
      if(this.page === "manage-customer-api") {
        this.getCustomerApi();
      }
      else {
        this.getVideoCallApi();
      }
    }
  }
  async getCustomerApi() {
    this.isLoading = true;
    const res = await this.dataServices.getCustomerApi();
    if (res.statusCode && res.statusCode === 200) {
      this.reqHeader = res.customerApi.headers;
      this.requestCurrentOption = res.customerApi.method;
      this.url = res.customerApi.url;
      this.reqHeader.push({
        key: '',
        value: '',
      })
    } else {
      const message = res.message ? res.message : 'Some thing went wrong while fetching data';
      this._snackbar.open(message, '', {
        duration: 2000,
      })
    }
    this.isLoading = false;
  }

  async getVideoCallApi() {
    this.isLoading = true;
    const res = await this.dataServices.getVideoCallCompleteApi();
    if (res.statusCode && res.statusCode === 200) {
      this.reqHeader = res.completeApi.headers;
      this.requestCurrentOption = res.completeApi.method;
      this.url = res.completeApi.url;
      this.reqHeader.push({
        key: '',
        value: '',
      })
    } else {
      const message = res.message ? res.message : 'Some thing went wrong while fetching data';
      this._snackbar.open(message, '', {
        duration: 2000,
      })
    }
    this.isLoading = false;
  }

  onCancelClick() {
    if (this.data) {
      this.dialog.closeAll();
    } else {
      if(this.page === "manage-customer-api") {
        this.getCustomerApi();
      }
      else {
        this.getVideoCallApi();
      }
    }
  }

  // Will be triggered when typed in textfield
  getKey(key, index) {

    console.log("key====>", key, index);
    this.reqHeader[index].key = key;
  }

  // Will be triggered when menu is clicked
  setKeyMenu(i, option) {
    console.log("key1111====>", i, option);
    this.reqHeader[i].key = option.value;
  }

  getValue(value, index) {
    this.reqHeader[index].value = value;
  }
  headerButtonClick(index) {
    // If the condition is to add new header
    if (index === this.reqHeader.length - 1) {
      if (this.reqHeader[index].key && this.reqHeader[index].value) {
        this.reqHeader.push({ key: '', value: '' });
      }
    }
    // If the condition is to delete the request header
    else {
      this.reqHeader.splice(index, 1);
    }
  }
  async createAPI() {
    // Pop'ing the req header to neglect the empty object that could be seen with the add button
    this.reqHeader.pop();
    // If dialog param is edit
    if (this.data && this.data.type === 'edit') {
      this.isLoading = true;
      const res = await this.dataServices.updateApiService(this.data.apiData.id, this.requestCurrentOption, this.url, this.reqHeader)
      if (res.message) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
        })
      }
      this.isLoading = false;
    }
    // If the dialog param is add
    else if (this.data && this.data.type === 'add') {
      this.isLoading = true;
      const res = await this.dataServices.createApiService(this.requestCurrentOption, this.url, this.reqHeader);
      if (res.message) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
          panelClass: ['redSnackBar'],
        })
      }
      this.isLoading = false;
    }
    // If not a dailog
    // Manage customer API
    else if(this.page === "manage-customer-api") {
      this.isLoading = true;
      const res = await this.dataServices.updateCustomerApi(this.requestCurrentOption, this.url, this.reqHeader);
      if (res.message) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
        })
      }
      this.getCustomerApi();
      this.isLoading = false;
    }
    // Manage Video call complete API
    else {
      this.isLoading = true;
      const res = await this.dataServices.updateVideoCallCompleteApi(this.requestCurrentOption, this.url, this.reqHeader);
      if (res.message) {
        this._snackbar.open(res.message, '', {
          duration: 2000,
        })
      }
      this.getVideoCallApi();
      this.isLoading = false;
    }
    // This is applicable only if a dialog
    if (this.data) { this.onCancelClick(); }
  }
  getUrl(url) {
    this.url = url;
  }
  onRequestChange(method) {
    this.requestCurrentOption = method.value;
  }
}
