import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { MatMenu } from '@angular/material/menu';


@Component({
  selector: 'sortdialog',
  templateUrl: './sortdialog.component.html',
  styleUrls: ['./sortdialog.component.scss']
})
export class SortdialogComponent implements OnInit {
  @ViewChild(MatMenu, { static: true }) menu: MatMenu;
  @Output() onFilterApply = new EventEmitter<any>();
  constructor() { }
  //for dateFilter
  datePickerValues = {
    dateCreated: {
      startDate: null,
      endDate: null,
    },
    dateModified: {
      startDate: null,
      endDate: null,
    }
  }
  //for checkboxfilters
  checkBoxFilters = {
    completed: null,
    stopped: null,
    locked: null,
    interrupted: null,
    saved: null,
    uploaded: null,
    uploadedToCRM: null,
  };

  //for reset values on inputs
  reset(event) {
    event.stopPropagation();
    Object.keys(this.checkBoxFilters).map(e => { this.checkBoxFilters[e] = null });
    this.datePickerValues = {
      dateCreated: {
        startDate: null,
        endDate: null,
      },
      dateModified: {
        startDate: null,
        endDate: null,
      }
    }
  }
  //for get data from filters
  applyFilters() {
    var status: any[] = Object.keys(this.checkBoxFilters).filter(e => {
      return this.checkBoxFilters[e] == true ? e.toString() : '';
    });
    const filters = {
      datecreated: this.datePickerValues.dateCreated,
      dateModified: this.datePickerValues.dateModified,
      status: status,
      checkboxFilters: this.checkBoxFilters,
    }
    this.onFilterApply.emit(filters);
  }
  ngOnInit(): void {
  }

  onCompleteChange(checked) {
  }



}
